<template>
  <div class="text-c pd-15">
    <div class="bgc"></div>
    <div class="btn_box border-fen" @click="reSet"></div>
  </div>
</template>
<script>
export default {
  methods: {
    reSet() {
      this.$router.back(-1);
    }
  }
};
</script>
<style scoped>
.btn_box {
  margin-top: 20px;
  font-size: 13px;
  text-align: center;
  color: #fff;
  border-radius: 20px;
  padding: 10px;
  background-image: linear-gradient(90deg, #ff5491, #fe4171);
}
</style>




