<template>
  <div>
    <div class="mar-b-10">
      <van-search placeholder="" v-model.trim="value" show-action>
        <div slot="action" @click="onSearch"></div>
      </van-search>
    </div>
    <div class="bgc merch-box">
      <div class="flexbox merchlist" v-for="(item,index) in 3" @click="merDetail">
        <img class="img_slt" src="../../assets/photo/banner.png">
        <div class="flex-column-space-between right">
          <div class="flex-jc-between">
            <div></div>
            <div>1.2km</div>
          </div>
          <div class="fsz12 flex-align-items">
            <van-rate v-model="rateval" :size="10" :count="5" color="#FFB10E" void-color="#ceefe8"/>
            <span>9</span>
          </div>
          <div class="pro_title fsz12" @click.stop="gonavigation">
            <img class="img_location" src="../../assets/photo/location.png">
            <div style="width: 190px; " class="newline">
              788(
              788(
              
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { Toast } from "vant";
export default {
  data() {
    return {
      value: "",
      rateval: "5"
    };
  },
  created() {
    let searchSession = JSON.parse(
      window.sessionStorage.getItem("searchSession")
    );
    if (searchSession) {
      this.value = searchSession.value;
    }
  },
  methods: {
    // 
    merDetail() {
      let searchSession = {
        value: this.value
      };
      window.sessionStorage.setItem(
        "searchSession",
        JSON.stringify(searchSession)
      );
      this.$router.push({ path: "/merchantDetail/" });
    },
    onSearch() {
      console.log(this.value);
      this.getsearch();
    },
    // 
    getsearch() {}
  }
};
</script>

<style scoped>
.merchlist {
  border-bottom: 10px solid #f0f0f0;
}
.merchlist:last-child {
  border: none;
}
.img_slt {
  width: 80px;
  height: 80px;
  padding: 15px;
  border-radius: 20px;
}
.right {
  width: 200px;
  padding: 15px 0;
}
.pro_title {
  display: flex;
  color: #979797;
  font-size: 12px;
}
.img_location {
  width: 11px;
  height: 14px;

  padding: 3px 5px 0 0;
}
</style>



