<template>
  <div>
    <div class="box">
      <div class="flex-jc-between border-b pd-15">
        <div></div>
        <div class="flex-align-items">
          <img class="img_weixin" src="../../assets/photo/weixin.png">
          <span></span>
        </div>
      </div>

      <div class="pd-15" style=" font-weight: 600;"></div>
      <div class="flex-align-items pd-15 border-b">
        <img class="qian_img" src="../../assets/photo/qian_icon.png">
        <input type="text" class="fs30" placeholder v-model="money">
      </div>
      <div class="pd-15">
        <span> 888.00</span>
        <span class="fc-fen" @click="allmoney"></span>
      </div>
    </div>

    <div class="flex-jc-center">
      <div class="btn text-c" @click="submit"></div>
    </div>
  </div>
</template>

<script>
import { Toast } from "vant";
export default {
  data() {
    return {
      money: ""
    };
  },
  methods: {
    allmoney() {
      this.money = 888.0;
    },
    submit() {
      Toast("");
    }
  }
};
</script>

<style scoped>
.fs30 {
  font-size: 30px;
}

.box {
  margin: 15px;
  overflow: hidden;
  background: rgba(255, 255, 255, 1);
  box-shadow: 0px 0px 16px 0px rgba(220, 221, 223, 0.19);
  border-radius: 10px;
  padding-bottom: 15px;
}
.img_weixin {
  width: 25px;
  height: 22px;
  margin-right: 10px;
}
.qian_img {
  width: 17px;
  height: 25px;
  margin-right: 15px;
}
.cart {
  font-size: 17px;
}
.fuhao {
  font-size: 30px;
  font-weight: 200;
  padding-right: 15px;
}
.btn {
  width: 299px;
  height: 40px;
  line-height: 40px;
  background: linear-gradient(90deg, #fe4171, #ff5491);
  border-radius: 20px;
  color: #fff;
  margin-top: 70px;
}
</style>



