<template>
  <div>
    <div class="pd-15"></div>
    <div class="nav flex-jc-around bgc">
      <div :class="{ 'selected': typenum==0 }" @click="typenum=0"></div>
      <div :class="{ 'selected': typenum==1 }" @click="typenum=1"></div>
      <!-- <div :class="{ 'selected': typenum==2 }" @click="typenum=2"></div> -->
    </div>
    <div class="bgc" v-show="typenum==0">
      <div class="pd-lr-15">
        <div></div>
        <van-radio-group v-model="radio" class="pd-tb-15">
          <div
            class="flex-jc-between mar-b-10"
            @click="radio = index"
            v-for="(item,index) in canceltext"
            :key="index"
          >
            <span>{{index+1}}{{item.text}}</span>
            <van-radio :name="index" checked-color="#FE4171"></van-radio>
          </div>
        </van-radio-group>
      </div>
    </div>
    <div class="btn" @click="toUpgrade"></div>
  </div>
</template>

<script>
import { Toast } from "vant";
export default {
  data() {
    return {
      typenum: 0,
      radio: 0,
      isdianzan: false,
      canceltext: [
        { id: 1, text: " 660" },
        { id: 2, text: " 2980" },
        { id: 3, text: " 9800" }
      ]
    };
  },
  created() {
    this.getData();
  },
  methods: {
    getData() {},
    // 
    toUpgrade() {
      console.log(this.typenum, "typenumtypenum");

      if (this.typenum == 0) {
        this.$router.push({ path: "/applicationUpgrade" });
      } else {
        this.$router.push({ path: "/menberCard" });
      }
    }
  }
};
</script>
<style scoped>
.nav {
  padding: 30px 10px;
}
.nav > div {
  width: 90px;
  height: 120px;
  line-height: 120px;
  display: inline-block;
  text-align: center;
  border-radius: 10px;
  box-sizing: border-box;
  margin-right: 3px;
  color: #7f7f7f;
  box-shadow: 2px 3px 8px 1px rgba(222, 220, 220, 0.5);
}
.nav .selected {
  color: #fff;
  background-image: linear-gradient(to bottom, #ff5491, #fe4171);
}

.btn {
  margin: 30px 15px;
  padding: 10px;
  text-align: center;
  color: #fff;
  font-size: 12px;
  border-radius: 20px;
  background-image: linear-gradient(90deg, #ff5491, #fe4171 100%);
}

.title {
  font-size: 16px;
  padding-top: 15px;
}
</style>





