<template>
  <div>
    <div class="bgc">
      <div class="pd-15"></div>
      <div class="border-b">
        <div
          class="flex-jc-between fz13 ser_box pd-lr-15"
          v-for="(item,index) in data.serve"
          :key="index"
        >
          <div>{{item.serve_name}}</div>
          <div>{{item.price}}</div>
        </div>
      </div>
      <div class="flex-jc-between border-b pd-15" @click="time">
        <div></div>
        <div class="fc-grey flex-align-items">
          <span class="fc-grey fz13"></span>
          <van-icon name="arrow"/>
        </div>
      </div>
      <div class="flex-jc-between border-b pd-15" @click="onshowmodel">
        <div></div>
        <div class="fc-grey flex-align-items">
          <span class="fc-grey fz13"></span>
          <van-icon name="arrow"/>
        </div>
      </div>
      <div class="pd-15"> {{data.mobile}}</div>
      <div class="footer bgc">
        <div class="btn text-c" @click="toSuccess"></div>
        <span class="fc-fen pd-lr-15">: {{data.total_price}}</span>
      </div>
    </div>

    <van-popup
      v-model="timemodel"
      position="bottom"
      :close-on-click-overlay="false"
      style="height:350px"
    >
      <div class="text-c position title">
        
        <div class="closeicon">
          <van-icon name="close" color="#979797" @click="time"/>
        </div>
      </div>
      <!-- <div class="select_container">
        <div style="position:relative;height:100%;width:100%" v-for="(item,index) in arr">
          <div class="left">
            <ul>
              <li
                @click="selectPro(index)"
                :class="{ 'pro_active': index==activeTitle}"
              >{{item.week_time}}</li>
            </ul>
          </div>
          <div class="right bgc" v-show="index==activeTitle">
            <div>
              <ul>
                <li v-for="(items,indexs) in item.time">
                  <div>
                    <div class="text-c" style="padding:20px 0">{{items.time}}</div>
                  </div>
                </li>
              </ul>
            </div>
          </div>
        </div>
      </div>-->

      <div class="select_container">
        <!---->
        <div class="left">
          <div class="left_item">
            <div
              v-for="(item,index) in week_times"
              @click="selectPro(index,item.id)"
              :class="{ 'pro_active': index==activeTitle}"
            >{{item.time}}</div>
          </div>
        </div>

        <!---->
        <div class="right bgc">
          <ul>
            <li v-for="(items,index) in times" @click="toDetail()">
              <div class="item-right">
                <div>{{items.time}}</div>
              </div>
            </li>
          </ul>
        </div>
      </div>
    </van-popup>
    <van-popup
      v-model="showmodel"
      position="bottom"
      style="height:350px"
      :close-on-click-overlay="false"
    >
      <div class="text-c position title">
        
        <div class="closeicon">
          <van-icon name="close" color="#979797" @click="onshowmodel"/>
        </div>
      </div>
      <div class="pd-lr-15 mar-b-10">
        <van-radio-group v-model="radio" @change="checkedName">
          <div
            class="flex-jc-between flex-align-items border-b pd-tb-15"
            @click="radio = index"
            v-for="(item,index) in data.beautician"
            :key="index"
          >
            <div class="flexbox">
              <div class="head_img">
                <img :src="item.avatar">
              </div>
              <div class="right_box">
                <div>{{item.username}}</div>
                <div class="fz11 fc-grey number">{{item.servicetimes}}</div>
                <div class="fz11 fc-fen"> {{item.grade}}%</div>
              </div>
            </div>
            <van-radio :name="item.id" checked-color="#FF508B"></van-radio>
          </div>
        </van-radio-group>
      </div>
    </van-popup>
  </div>
</template>
<script>
import { Toast } from "vant";
export default {
  data() {
    return {
      data: "",
      radio: 0,
      timearr: [],
      week_time: [],
      times: [],
      titlelist: [
        { name: " 01-22" },
        { name: " 01-22" },
        { name: "" },
        { name: "" },
        { name: "" }
      ],

      activeTitle: 0,
      showmodel: false,
      timemodel: false,
      beautician_id: ""
    };
  },

  created() {
    this.getData();
  },
  methods: {
    getData() {
      Toast.loading({ mask: true, message: "..." });
      this.axios.get(this.API + "/api/v1/serve/next").then(res => {
        console.log(res.data, "serve/next");
        let resdata = res.data;
        if (resdata.code == 200) {
          this.data = resdata.data;
          this.timearr = resdata.data.serve_time;
          let week_times = [];
          for (let v of this.timearr) {
            console.log(v.week_time);
            let week_time = {
              time: v.week_time
            };
            console.log(this.timearr[0].time);
            this.times = this.timearr[0].time;

            week_times.push(week_time);
            console.log(week_times);
          }
          this.week_times = week_times;
          Toast.clear();
        } else {
          Toast.clear();
          Toast(resdata.msg);
        }
      });
    },
    onshowmodel() {
      if (this.showmodel) {
        this.showmodel = false;
      } else {
        this.showmodel = true;
      }
    },
    checkedName(res) {
      console.log(res);
      this.beautician_id = res;
      let beautiarr = this.data.beautician;

      for (let v of beautiarr) {
        // if ((v.id = this.beautician_id)) {
        //   console.log(v.id);
        //   console.log(v.username);
        // }
      }
      this.showmodel = false;
    },

    time() {
      if (this.timemodel) {
        this.timemodel = false;
      } else {
        this.timemodel = true;
      }
    },
    selectPro(index) {
      console.log(index, this.week_times[index].week_time);
      this.activeTitle = index;
    },
    toSuccess() {
      // this.$router.push({ path: "/successful/" });

      Toast.loading({ mask: true, message: "..." });

      let param = new URLSearchParams();
      param.append("serve_ids", this.$route.query.serve_ids);
      param.append("beautician_id", this.beautician_id);
      param.append("serve_time", 10);
      param.append("total_price", this.data.total_price);
      this.axios.post(this.API + "/api/v1/order/confirm", param).then(res => {
        console.log(res.data, "order/confirm");
        this.typenum == 3;
        Toast.clear();
        Toast(resdata.msg);
      });
    }
  }
};
</script>

<style scoped>
.fz11 {
  font-size: 11px;
}
.fz13 {
  font-size: 13px;
}
.ser_box {
  padding-bottom: 10px;
}

.footer {
  display: flex;
  flex-direction: row-reverse;
  width: 100%;
  position: fixed;
  bottom: 0;
  height: 50px;
  line-height: 50px;
}
.btn {
  width: 100px;
  height: 50px;
  line-height: 50px;
  color: #fff;
  background: linear-gradient(90deg, #ff5491, #fe4171);
}

.title .closeicon {
  position: absolute;
  right: 10px;
  top: 18px;
  font-size: 20px;
}
.title {
  font-size: 16px;
  padding: 15px 0;
  border-bottom: 1px solid #b7b7b7;
}
.right_box {
  margin-left: 10px;
}
.number {
  padding: 5px 0;
}
.head_img > img {
  width: 50px;
  height: 50px;
  border-radius: 50%;
}

/*  */
.select_container {
  height: 280px;
  overflow: hidden;
  background: #fff;
  display: flex;
  position: relative;
}
.left {
  width: 150px;
  height: 280px;
  position: absolute;
  overflow: hidden;
  top: 0;
  left: 0;
}
.left_item {
  width: 150px;
  height: 100%;
  list-style: none;
  overflow-x: hidden;
  overflow-y: scroll;
  background: #eee;
}
.left_item > div {
  padding: 15px 5px;
  text-align: center;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.pro_active {
  background: #fff;
  color: #fc4f88;
  border-left: 3px solid #fc4f88;
  font-weight: 700px;
}
.right {
  width: 160px;
  height: 280px;
  position: absolute;
  top: 0;
  left: 150px;
}
.right ul {
  width: 160px;
  height: 100%;
  text-align: center;
  list-style: none;
  overflow-x: hidden;
  overflow-y: scroll;
}

.item {
  display: flex;
  overflow: hidden;
  text-align: center;
  width: 100%;
  padding: 10px;
  border-bottom: 1px solid #eee;
}

.item-right {
  width: 100%;
  padding: 15px 5px;
  border-bottom: 1px solid #eee;
  text-align: center;
}
</style>



