<template>
  <div>
    <div class="fixed" v-show="isshow">
      <div class="text-c"></div>
    </div>
    <div class="flex-jc-center bgc">
      <div class="banner bgc">
        <van-swipe :autoplay="3000" indicator-color="#FC9745">
          <van-swipe-item v-for="(item, index) in images" :key="index">
            <div class="img_box flex-center">
              <img :src="item.img_url">
            </div>
          </van-swipe-item>
        </van-swipe>
      </div>
    </div>

    <div class="flex-jc-around bgc">
      <div class="tg">
        <router-link to="/CompanyIntroduction">
          <span class="flex-jc-center">
            <img src="../assets/photo/gsjs.png">
          </span>
          <span></span>
        </router-link>
      </div>
      <div class="tg">
        <router-link to="/shopstyle">
          <span class="flex-jc-center">
            <img src="../assets/photo/dpfc.png">
          </span>
          <span></span>
        </router-link>
      </div>
      <div class="tg">
        <router-link to="/operationalGuide">
          <span class="flex-jc-center">
            <img src="../assets/photo/czzy.png">
          </span>
          <span></span>
        </router-link>
      </div>
      <div class="tg">
        <router-link to="/teamstyle">
          <span class="flex-jc-center">
            <img src="../assets/photo/tdfc.png">
          </span>
          <span></span>
        </router-link>
      </div>
    </div>

    <div class="flexbox bgc">
      <div class="tg">
        <router-link to="/navigation">
          <span class="flex-jc-center">
            <img src="../assets/photo/yjdh.png">
          </span>
          <span></span>
        </router-link>
      </div>
      <div class="tg">
        <router-link to="/newActivities">
          <span class="flex-jc-center">
            <img src="../assets/photo/zxhd.png">
          </span>
          <span></span>
        </router-link>
      </div>
      <div class="tg" @click="newGoods">
        <span class="flex-jc-center">
          <img src="../assets/photo/xptj.png">
        </span>
        <span></span>
      </div>
      <div class="tg">
        <router-link to="/merchantEntry">
          <span class="flex-jc-center">
            <img src="../assets/photo/sjrz.png">
          </span>
          <span></span>
        </router-link>
      </div>
    </div>
    <div id="notice" class="pd-lr-15 bgc">
      <van-notice-bar text=":" left-icon="volume-o"/>
    </div>
    <div class="bgc">
      <div class="title flex-align-items">
        <img src="../assets/photo/re.png" class="img_rx"> 
      </div>

      <div class="product" v-for="(item,index) in goods" @click="toDetail(item.id)">
        <div class="img_pro">
          <img :src="item.cover_url">
        </div>
        <div class="pro_title newline">{{item.name}}</div>

        <div class="pro_price">
          <span class="n_price fc-fen">{{item.mall_price}}</span>
          <del class="o_price fc-grey">{{item.market_price}}</del>
        </div>
      </div>
    </div>
    <div class="height"></div>
  </div>
</template>

<script>
import { Toast } from "vant";
import { getWXSign } from "../anxios/api";
import wx from "weixin-js-sdk";
export default {
  data() {
    let self = this;

    return {
      images: [
        "http://www.pptbz.com/pptpic/UploadFiles_6909/201306/2013062320262198.jpg",
        "http://www.pptbz.com/pptpic/UploadFiles_6909/201306/2013062320262198.jpg",
        "http://www.pptbz.com/pptpic/UploadFiles_6909/201306/2013062320262198.jpg"
      ],
      isshow: false,
      goods: []
    };
  },

  created() {
    let code = window.location.search.split("&")[0].slice(6);

    if (JSON.parse(window.localStorage.getItem("access_token"))) {
    } else {
      if (this.$route.query.store_id) {
        let store_id = this.$route.query.store_id;
        window.localStorage.setItem("store_id", JSON.stringify(store_id));
        this.login();
      } else {
        this.isshow = true;
      }
      if (code) {
        this.getCode(code);
      }
    }

    this.getdata();
    console.log(JSON.parse(window.localStorage.getItem("access_token")));
    console.log(wx, "wx");
  },

  methods: {
    login() {
      Toast.loading({ mask: true, message: "..." });
      if (this.$route.query.store_id) {
        this.axios
          .get(this.API + "/api/v1/wechat/login", {
            params: {
              store_id: this.$route.query.store_id
            }
          })
          .then(res => {
            console.log(res.data, "login");
            let resdata = res.data;
            if (resdata.code == 200) {
              // code
              window.location.href = resdata.data;

              console.log(this.$route.query.code);

              Toast.clear();
            } else if (resdata.code == 40006) {
              this.isshow = true;
            } else {
              Toast.clear();
              Toast(resdata.msg);
            }
          });
      }
    },
    getCode(code) {
      window.localStorage.setItem("code", JSON.stringify(code));
      Toast.loading({ mask: true, message: "..." });
      this.axios
        .get(this.API + "/api/v1/wechat/register", {
          params: {
            code: code,
            store_id: JSON.parse(window.localStorage.getItem("store_id"))
          }
        })
        .then(res => {
          console.log(res.data, "register");

          let resdata = res.data;
          if (resdata.code == 200) {
            window.localStorage.setItem(
              "access_token",
              JSON.stringify(resdata.data.token)
            );

            if (resdata.data.is_mobile == 0) {
              this.$router.push({ path: "/register/" });
            }

            this.isshow = false;
            Toast.clear();
          } else {
            this.isshow = true;
            Toast.clear();
            Toast(resdata.msg);
          }
        });
    },
    getdata() {
      Toast.loading({ mask: true, message: "..." });
      this.axios.get(this.API + "/api/v1/home").then(res => {
        console.log(res.data, "home");

        let resdata = res.data;
        if (resdata.code == 200) {
          this.images = resdata.data.banner;
          this.goods = resdata.data.goods;
          Toast.clear();
        } else if (resdata.code == 40006) {
          this.isshow = true;
        } else {
          Toast.clear();
          Toast(resdata.msg);
        }
      });
    },
    //
    toDetail(id) {
      this.$router.push({
        path: "/ProductDetail",
        query: {
          id: id
        }
      });
    },
    // 
    newGoods() {
      this.$router.push({
        path: "/newArrivals",
        query: {
          id: this.$route.query.store_id
        }
      });
    }
  }
};
</script>

<style>
#notice {
  padding-top: 10px;
}
#notice .van-notice-bar {
  background-color: #faf2f2;
  border-radius: 20px;
  height: 30px;
}
</style>

<style scoped>
.fixed {
  overflow: hidden;
  position: fixed;
  left: 0;
  top: 0;
  height: 100vh;
  width: 100%;
  background: #fff;
  z-index: 9;
  font-size: 18px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.f12 {
  font-size: 12px;
}

.top_sear {
  width: 100%;
  height: 44px;
}

.sear {
  height: 32px;
  border-radius: 5px;
  background: #f6f5f5;
}

.sear .van-icon {
  padding-left: 10px;
}

.dw {
  width: 50%;
  height: 44px;
  line-height: 44px;
  margin-left: 13px;
  font-size: 13px;
}
.dw span {
  display: inline-block;
  width: 50px;
  height: 100%;
}

.banner {
  width: 320px;
  height: 184px;
}
.img_box {
  width: 320px;
  height: 184px;
}
.img_box img {
  width: 100%;
  height: 100%;
}
.tg {
  width: 80px;
  text-align: center;
  line-height: 30px;
  padding-top: 10px;
}

.tg img {
  display: block;
  width: 48px;
  height: 48px;
}

.title {
  font-size: 17px;
  font-weight: 600;
  padding: 10px;
  height: 30px;
  line-height: 30px;
}

.img_rx {
  width: 14px;
  height: 20px;
  margin-right: 10px;
}

.product {
  display: inline-block;
  width: 140px;
  height: 249px;
  border-radius: 10px;
  box-shadow: 0px 3px 7px 0px rgba(238, 238, 238, 1);
  margin: 0 0 15px 10px;
}

.img_pro {
  width: 143px;
  height: 160px;
}
.img_pro img {
  width: 100%;
  height: 100%;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
}

.pro_title {
  width: 122px;
  height: 35px;
  margin: 10px 0 10px 10px;
}

.pro_price {
  width: 122px;
  height: 20px;
  line-height: 20px;
  margin-left: 10px;
}

.n_price {
  font-size: 15px;
}
.o_price {
  font-size: 12px;
  margin-left: 10px;
}
.height {
  width: 100%;
  height: 60px;
  background: #fff;
}
</style>


