<template>
  <div>
    <div class="bgc">
      <div class="flexbox pd-15" v-for="(item,index) in goods">
        <img class="img_slt" :src="item.cover_url">
        <div style="margin-left: 10px;">
          <div class="text-line pro_title">{{item.name}}</div>
          <div class="fsz12 fc-grey" style="padding:10px 0">{{item.num}}</div>
          <div class="fc-fen">{{item.price}}</div>
        </div>
      </div>
      <div class="pd-15 flex-jc-between border-b border-top">
        <div>:</div>
        <div>{{all_price}}</div>
      </div>

      <div class="pd-15 flex-jc-between flex-align-items border-b">
        <div>:</div>

        <div class="nav bgc">
          <div :class="{ 'selected': typenum==2 }" @click="typenum=2"></div>
          <div :class="{ 'selected': typenum==1 }" @click="typenum=1"></div>
        </div>
      </div>
      <div v-show="typenum==2" class="border-b" style="height:100px">
        <div class="pd-lr-15" style=" padding-top: 10px;">:</div>
        <van-cell @click="go('/locationList')">
          <template slot="title">
            <div>{{store.name}}</div>
            <div>{{store.province+store.city+store.district+store.address}}</div>
          </template>
        </van-cell>
      </div>
      <div v-show="typenum==1" class="border-b" style="height:100px">
        <div class="pd-lr-15" style=" padding-top: 10px;">:</div>
        <van-cell is-link @click="goAddress">
          <template slot="title">
            <div>
              {{address.consignee}}
              {{address.mobile}}
              <span class="moren"></span>
            </div>
            <div>{{address.province+address.city+address.district+address.address}}</div>
          </template>
        </van-cell>
      </div>
      <div class="pd-15 flex-jc-between border-b">
        <div>:</div>
        <div>{{carriage}}</div>
      </div>
      <div class="pd-15">
        :
        <input v-model="remark">
      </div>
    </div>

    <div class="bgc footer flex-align-items">
      <div class="fc-fen pd-lr-15">: {{all_price}}</div>

      <div class="btn" @click="gopay(address.id)"></div>
    </div>
  </div>
</template>
<script>
import { Dialog, Toast } from "vant";
export default {
  data() {
    return {
      typenum: 2,
      count: "",
      address: "",
      goods: [],
      store: [],
      remark: "",
      order_id: "",
      goods_id: "",
      all_price: "",
      carriage: ""
    };
  },
  created() {
    let typenumSession = JSON.parse(
      window.sessionStorage.getItem("typenumSession")
    );

    if (typenumSession) {
      this.typenum = typenumSession.typenum;
    }
    this.getData();
  },
  methods: {
    getData() {
      Toast.loading({ mask: true, message: "..." });

      this.axios.get(this.API + "/api/v1/goods/confirm").then(res => {
        console.log(res.data, "confirm");
        let resdata = res.data;
        if (resdata.code == 200) {
          this.address = resdata.data.address;
          this.goods = resdata.data.goods.list;
          this.store = resdata.data.store;
          this.count = resdata.data.goods.list[0].num;
          this.goods_id = resdata.data.goods.id;
          this.all_price = resdata.data.goods.all_price;
          this.carriage = resdata.data.goods.carriage;

          Toast.clear();
        } else {
          Toast.clear();
          Toast(resdata.msg);
        }
      });
    },
    goAddress() {
      let typenumSession = {
        typenum: this.typenum
      };
      window.sessionStorage.setItem(
        "typenumSession",
        JSON.stringify(typenumSession)
      );
      this.$router.push({
        path: "/addressList"
      });
      console.log(this.typenum);
    },
    gopay(id) {
      Toast.loading({ mask: true, message: "..." });
      //
      let products = [];
      //     
      for (let v of this.goods) {
        //ID   goods_id  num
        let product = {
          goods_id: v.id,
          num: v.num
        };
        //
        products.push(product);
      }
      console.log(JSON.stringify(products), "products");

      let param = new URLSearchParams();
      param.append("goods", JSON.stringify(products));
      param.append("remark", this.remark || "");
      param.append("delivery_type", this.typenum);
      param.append("address_id", id || "");
      this.axios.post(this.API + "/api/v1/goods/submit", param).then(res => {
        console.log(res.data, "submit");
        let resdata = res.data;
        if (resdata.code == 200) {
          this.order_id = resdata.data.order_id;
          console.log(this.order_id);
          Toast(resdata.msg);
        } else {
          Toast.clear();
          Toast(resdata.msg);
        }
      });
    }
  }
};
</script>
<style scoped>
.img_slt {
  width: 80px;
  height: 80px;
  border-radius: 5px;
}

.pro_title {
  width: 190px;
}
.border-top {
  border-top: 10px solid #f0f0f0;
}
.fsz12 {
  font-size: 12px;
}

.nav > div {
  height: 25px;
  padding: 0 5px;
  line-height: 25px;
  display: inline-block;
  border-radius: 5px;
  text-align: center;
  border: 1px solid black;
  box-sizing: border-box;
  font-size: 13px;
  margin-left: 5px;
}
.nav .selected {
  color: #fe4476;
  border: 1px solid #fe4476;
}

.moren {
  background: linear-gradient(90deg, #ff5491, #fe4171);
  color: #fff;
  font-size: 10px;
  padding: 2px 5px;
  border-radius: 5px;
}

.footer {
  width: 100%;
  height: 40px;
  position: fixed;
  bottom: 0;
  justify-content: flex-end;
}
.btn {
  height: 40px;
  line-height: 40px;
  width: 108px;
  font-size: 14px;
  text-align: center;
  background: linear-gradient(90deg, #ff5491, #fe4171);
  color: #fff;
}
</style>



