<template>
  <div>
    <div class="pd-15 flexbox" v-show="order_status==0">
      <img class="img_daifu" src="../../assets/photo/daifukuan.png">
      <div>
        <div></div>
        <div class="fz13 pd-t">{{hour}}{{minute}}</div>
      </div>
    </div>
    <div class="pd-15 flexbox" v-show="order_status==1">
      <img class="img_daifa" src="../../assets/photo/daifahuo.png">
      <div>
        <div></div>
        <div class="fz13 pd-t"></div>
      </div>
    </div>
    <div class="pd-15 flexbox" v-show="order_status==2">
      <img class="img_daishou" src="../../assets/photo/daishouhuo.png">
      <div>
        <div></div>

        <div class="flex-jc-between flex-align-items pd-10 wuliu">
          <span class="fz12"></span>

          <van-icon name="arrow"/>
        </div>
        <div class="fz13">201901-30 11:33:34</div>
      </div>
    </div>
    <div class="pd-15 flex-align-items" v-show="order_status==3">
      <img class="img_daishou" src="../../assets/photo/daishouhuo.png">
      <div></div>
    </div>
    <div class="pd-15 flexbox" v-show="order_status==4">
      <img class="img_daipinglun" src="../../assets/photo/daipinglun.png">
      <div>
        <div></div>

        <div class="flex-jc-between flex-align-items pd-10 wuliu">
          <span class="fz12">
            
            
          </span>

          <van-icon name="arrow"/>
        </div>
        <div class="fz13">201901-30 11:33:34</div>
      </div>
    </div>
    <div class="pd-15 flexbox bgc mar-b-10">
      <img class="img_location" src="../../assets/photo/location.png">
      <div>
        <div>{{data.username}} {{data.mobile}}</div>
        <div class="fz12 pd-t">{{data.address}}</div>
      </div>
    </div>
    <div class="pd-15 bgc mar-b-10">
      <div class="flexbox product" v-for="(item,index) in data.order_goods">
        <img class="img_pro" :src="item.goods_img">
        <div>
          <div class="mar-b-10">{{item.goods_name}}</div>
          <div class="fz12 fc-grey">{{item.goods_num}}</div>
        </div>
      </div>
    </div>
    <div class="pd-15 bgc mar-b-10">
      <div class="flex-jc-between">
        <div>
          <div class="flex-jc-between">
            <div>:</div>
          </div>
        </div>
        <div class="fz12 fc-grey">{{data.order_sn}}</div>
      </div>
      <div class="flex-jc-between pd-t20">
        <div>:</div>
        <div class="fz12 fc-grey">{{data.total_price}}</div>
      </div>
      <div class="flex-jc-between pd-t20">
        <div>:</div>
        <div class="fz12 fc-grey" v-show="data.delivery_method==1"></div>
        <div class="fz12 fc-grey" v-show="data.delivery_method==2"></div>
      </div>
      <div class="flex-jc-between pd-t20">
        <div>:</div>
        <div class="fz12 fc-grey">0.00</div>
      </div>
      <div class="flex-jc-between pd-t20">
        <div>:</div>
        <div class="fz12 fc-grey">{{data.remark }}</div>
      </div>

      <div class="flex-jc-between pd-t20 fc-fen">
        <div></div>
        <div class="fz12">259.00</div>
      </div>
    </div>

    <div class="btn_box pd-10 bgc" v-show="order_status==0">
      <span class="fc-fen text-c border-fen"></span>
      <span class="fc-grey text-c border" @click="onshowmodel"></span>
    </div>
    <div class="btn_box pd-10 bgc" v-show="order_status==2" @click="confirm">
      <span class="fc-fen text-c border-fen"></span>
    </div>
    <div class="btn_box pd-10 bgc" v-show="order_status==3" @click="confirm">
      <span class="fc-fen text-c border-fen"></span>
    </div>
    <div class="btn_box pd-10 bgc" v-show="order_status==4" @click="del">
      <!-- <span class="fc-fen text-c border-fen" @click="toComment"></span> -->
      <span class="fc-grey text-c border"></span>
    </div>
    <van-popup v-model="showmodel" position="bottom" :close-on-click-overlay="false">
      <div class="text-c position title">
        
        <div class="closeicon">
          <van-icon name="close" @click="onshowmodel"/>
        </div>
      </div>
      <div class="pd-lr-15">
        <div class="pd-tb-15">,</div>
        <van-radio-group v-model="radio" @change="checkedName">
          <div
            class="flex-jc-between mar-b-10"
            @click="radio = index"
            v-for="(item,index) in canceltext"
            :key="index"
          >
            <span>{{index+1}}{{item.text}}</span>
            <van-radio :name="index" checked-color="#FE4171"></van-radio>
          </div>
        </van-radio-group>
      </div>
      <div class="pd-15">
        <div class="btn text-c" @click="cancelOrder"></div>
      </div>
    </van-popup>

    <van-dialog v-model="show" show-cancel-button :before-close="beforeClose">
      <div class="text-c pd-15"></div>
    </van-dialog>
  </div>
</template>

<script>
import { Toast } from "vant";
export default {
  data() {
    return {
      hour: "",
      minute: "",
      data: "",
      cancel_remark: "/",
      order_status: "",
      radio: 0,
      showmodel: false,
      show: false,
      canceltext: [
        { id: 1, text: "/" },
        { id: 2, text: "" },
        { id: 3, text: "" },
        { id: 4, text: "" }
      ]
    };
  },
  created() {
    this.getData();
  },
  methods: {
    getData() {
      Toast.loading({ mask: true, message: "..." });
      this.axios
        .get(this.API + "/api/v1/order/detail", {
          params: {
            order_id: this.$route.query.id
          }
        })
        .then(res => {
          console.log(res.data, "order/detail");
          let resdata = res.data;
          if (resdata.code == 200) {
            this.hour = resdata.data.hour;
            this.minute = resdata.data.minute;
            console.log(resdata.data.hour);
            this.data = resdata.data.order;
            this.order_status = resdata.data.order.order_status;
            Toast.clear();
          } else {
            Toast.clear();
            Toast(resdata.msg);
          }
        });
    },
    onshowmodel() {
      if (this.showmodel) {
        this.showmodel = false;
      } else {
        this.showmodel = true;
      }
    },

    // 
    cancelOrder() {
      Toast.loading({ mask: true, message: "..." });

      let param = new URLSearchParams();
      param.append("order_id", this.$route.query.id);
      param.append("cancel_remark", this.cancel_remark);

      this.axios.post(this.API + "/api/v1/order/cancel", param).then(res => {
        console.log(res.data, "order/cancel");
        let resdata = res.data;
        this.$router.back(-1);
        Toast.clear();
        Toast(resdata.msg);
      });
      this.showmodel = false;
    },
    del() {
      this.show = true;
    },
    checkedName(res) {
      console.log(res);
      if (res == 0) {
        this.cancel_remark = "/";
      } else if (res == 1) {
        this.cancel_remark = "";
      } else if (res == 2) {
        this.cancel_remark = "";
      } else {
        this.cancel_remark = "";
      }
    },

    //  
    confirm() {
      console.log(this.order_id);
      Toast.loading({ mask: true, message: "..." });

      let param = new URLSearchParams();
      param.append("order_id", this.$route.query.id);
      this.axios.post(this.API + "/api/v1/order/confirm", param).then(res => {
        console.log(res.data, "order/confirm");
        this.getData();
        Toast.clear();
        Toast(resdata.msg);
      });
    },
    // 
    beforeClose(action, done) {
      if (action === "confirm") {
        // setTimeout(done, 500);

        Toast.loading({ mask: true, message: "..." });

        let param = new URLSearchParams();
        param.append("order_id", this.$route.query.id);

        this.axios.post(this.API + "/api/v1/order/del", param).then(res => {
          console.log(res.data, "order/del");
          this.$router.back(-1);
          Toast.clear();
          Toast(resdata.msg);
        });
      } else {
        done();
      }
    }
    // toComment() {
    //   this.$router.push({
    //     path: "/comment/",
    //     query: {
    //       id: this.$route.query.id
    //     }
    //   });
    // }
  }
};
</script>

<style scoped>
.pd-t {
  padding-top: 10px;
}
.pd-t20 {
  padding-top: 20px;
}
.pd-10 {
  padding: 10px 0;
}
.fz13 {
  font-size: 13px;
}
.fz12 {
  font-size: 12px;
}
.img_daifu {
  width: 17px;
  height: 17px;
  margin-right: 10px;
}
.img_daifa {
  width: 20px;
  height: 17px;
  margin-right: 10px;
}
.img_daishou {
  width: 20px;
  height: 19px;
  margin-right: 10px;
}
.img_daipinglun {
  width: 21px;
  height: 19px;
  margin-right: 10px;
}
.wuliu {
  width: 265px;
}
.img_location {
  width: 13px;
  height: 16px;
  margin: 2px 10px 0 0;
}
.product {
  margin-bottom: 10px;
}
.product:last-child {
  margin-bottom: 0px;
}
.img_pro {
  width: 60px;
  height: 65px;
  margin-right: 10px;
  border-radius: 5px;
}
.btn_box {
  display: flex;
  flex-direction: row-reverse;
  margin-top: 30px;
}
.btn_box span {
  width: 80px;
  height: 30px;
  margin-right: 15px;
  line-height: 30px;
  border-radius: 5px;
}

.tag .dot {
  position: absolute;
  top: 0;
  right: 0;
}

.list {
  padding: 14px;
}
.model {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 100;
}
.model .main {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
}
.btn {
  height: 30px;
  line-height: 30px;
  border-radius: 20px;
  color: #fff;
  background-image: linear-gradient(90deg, #fe4171, #fe4171 100%);
}
.codeimg {
  width: 180px;
  height: 180px;
  margin-bottom: 20px;
}
.closeimg {
  width: 34px;
  height: 34px;
  position: absolute;
  right: 20px;
  top: 100px;
  font-size: 34px;
}
.title .closeicon {
  position: absolute;
  right: 10px;
  top: 10px;
  font-size: 20px;
}
.title {
  font-size: 16px;
  padding-top: 15px;
}
</style>



