<template>
  <div>
    <div class="bgc pd-15 border-b">
      <div class="flex-align-items">
        <img class="img_logo border" src="../../assets/photo/yanzhengma.png">
        <div>
          <div class="fc-fen fz15"></div>
          <div class="fc-grey fz12">{{data.created_at}}</div>
        </div>
      </div>

      <div class="pd-tb-15" v-html="data.description"></div>
      <div class="imglist_box">
        <div class="imglist" v-for="(item,index) in data.images" :key="index">
          <img class="itemimg" :src="item.img_url">
        </div>
      </div>
    </div>
    <div class="flex-align-items bgc img_box">
      <div class="text-c" @click="ondianzan(data.id)">
        <div>
          <img v-if="data.is_like==1" class="img_commit" src="../../assets/photo/dianzans.png" alt>
          <img v-else class="img_commit" src="../../assets/photo/dianzan.png" alt>
        </div>
      </div>
      <div class="border-l text-c">
        <img class="img_fenxiang" src="../../assets/photo/fenxiang.png">
      </div>
    </div>
  </div>
</template>

<script>
import { Toast, Notify } from "vant";
export default {
  data() {
    return {
      imglist: [
        "http://www.pptbz.com/pptpic/UploadFiles_6909/201306/2013062320262198.jpg",
        "http://www.pptbz.com/pptpic/UploadFiles_6909/201306/2013062320262198.jpg",
        "http://www.pptbz.com/pptpic/UploadFiles_6909/201306/2013062320262198.jpg"
      ],
      isdianzan1: false,
      data: ""
    };
  },
  created() {
    this.getData();
  },
  methods: {
    ondianzan(id) {
      Toast.loading({ mask: true, message: "..." });
      this.axios
        .get(this.API + "/api/v1/material/like", {
          params: {
            material_id: id
          }
        })
        .then(res => {
          console.log(res.data, "material/like");
          let resdata = res.data;
          if (resdata.code == 200) {
            this.getData();
            Notify({
              message: resdata.msg,
              duration: 1000,
              background: "#1989fa"
            });
            Toast.clear();
          } else {
            Toast.clear();
            Toast(resdata.msg);
          }
        });
    },
    getData() {
      Toast.loading({ mask: true, message: "..." });
      this.axios
        .get(this.API + "/api/v1/material/detail", {
          params: {
            material_id: this.$route.query.id
          }
        })
        .then(res => {
          console.log(res.data, "material/detail");
          let resdata = res.data;
          if (resdata.code == 200) {
            this.data = resdata.data;
            Toast.clear();
          } else {
            Toast.clear();
            Toast(resdata.msg);
          }
        });
    }
  }
};
</script>
<style scoped>
.fz12 {
  font-size: 12px;
}
.border-l {
  border-left: #f0f0f0 1px solid;
}
.img_logo {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  margin-right: 10px;
}

.itemimg {
  width: 290px;
  height: 130px;
  margin-bottom: 10px;
}
.img_box {
  height: 40px;
}
.img_box > div {
  width: 200px;
}
.img_commit {
  width: 16px;
  height: 16px;
}
.img_fenxiang {
  width: 15px;
  height: 16px;
}
.photo {
  padding-top: 15px;
}
.photo img {
  border-radius: 10px;
}
</style>





