<template>
  <div class="bgc">
    <div class="pd-15">
      <div class="total_pro pd-15">
        <div></div>
        <div class="total_money text-c"> 888.00</div>
        <div class="text-c pd-tb-10"></div>
        <div class="flex-jc-center" @click="toCash">
          <div class="btn text-c"></div>
        </div>
      </div>
    </div>

    <div class="nav flex-jc-around">
      <div :class="{ 'selected': navIndex==0 }" @click="navIndex=0"></div>
      <div :class="{ 'selected': navIndex==1 }" @click="navIndex=1"></div>
    </div>
    <div style="padding-bottom:20px" v-show=" navIndex==0">
      <div class="profit_box pd-lr-15 mar-b-10" v-for="(item,index) in profitlist" :key="index">
        <div class="flex-jc-between flex-align-items profi-titem pd-15">
          <div>
            <div class="fz15" style=" font-weight: 600;"></div>
            <div class="fz13 pd-tb-10 fc-grey">2018-08-02</div>
          </div>
          <div class="fc-fen fz15">+10.00</div>
        </div>
      </div>
    </div>
    <div style="padding-bottom:20px" v-show=" navIndex==1">
      <div class="profit_box pd-lr-15 mar-b-10" v-for="(item,index) in profitlist" :key="index">
        <div class="flex-jc-between flex-align-items profi-titem pd-15">
          <div>
            <div class="fz15" style=" font-weight: 600;"></div>
            <div class="fz13 pd-tb-10 fc-grey">2018-08-02</div>
          </div>
          <div class="fz15 fc-grey">-10.00</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      profitlist: [{}, {}, {}],
      navIndex: 0
    };
  },
  created() {
    this.getData();
  },
  methods: {
    getData() {
      Toast.loading({ mask: true, message: "..." });

      this.axios
        .get(this.API + "/api/v1/serve/my", {
          params: {
            type: this.typenum + 1
          }
        })
        .then(res => {
          console.log(res.data, "serve");
          let resdata = res.data;

          if (resdata.code == 200) {
            this.servelist = resdata.data;
          } else {
            Toast.clear();
            Toast(resdata.msg);
          }
        });
    },
    toCash() {
      this.$router.push({ path: "/cash" });
    }
  }
};
</script>


<style scoped>
.fs15 {
  font-size: 15px;
}

.fz13 {
  font-size: 13px;
}

.pd-tb-10 {
  padding: 10px 0;
}
.nav {
  padding: 0 50px 10px 50px;
  background: #fff;
}
.nav > div {
  height: 30px;
  line-height: 30px;
  display: inline-block;
  text-align: center;
  box-sizing: border-box;
  margin-right: 3px;
}
.nav .selected {
  color: #fe4171;
  border-bottom: 3px solid #ff5491;
}

.total_pro {
  height: 170px;
  background-color: rgba(255, 255, 255, 1);
  box-shadow: 2px 3px 8px 1px rgba(222, 220, 220, 0.5);
  border-radius: 10px;
}

.total_money {
  font-size: 30px;
  font-weight: 700;
  margin-top: 30px;
}
.btn {
  width: 70px;
  height: 27px;
  line-height: 27px;
  border-radius: 20px;
  background-image: linear-gradient(90deg, #ff5491, #fe4171);
  color: #fff;
  margin-top: 5px;
}

.profi-titem {
  height: 40px;
  background-color: rgba(255, 255, 255, 1);
  box-shadow: 2px 3px 8px 1px rgba(222, 220, 220, 0.5);
  border-radius: 10px;
}
</style>





