<template>
  <div>
    <!-- <div class="flex-jc-between shop"  :style="bgimg"> -->
    <div class="position">
      <img class="img_shop" :src="data.cover_url">
      <div class="shop_mess pd-15">
        <div class="shop_name">{{data.name}}</div>
        <div class="fz12 pd-tb-15">: 10:30~23:00</div>
        <div class="flex-jc-between" @click="location(data)">
          <div class="fz12 address">{{data.address}}</div>
          <img class="img_location" src="../../assets/photo/locations.png">
        </div>
      </div>
    </div>

    <div class="shopnav position" id="shopnav">
      <van-tabs @click="ontab" v-model="ind">
        <van-tab :title="item" v-for="(item,index) in navtitle" :key="index">
          <div v-show="ind==0">
            <ul>
              <li v-for="(item,index) in servelist" @click="selectIndex(index)">
                <input
                  type="checkbox"
                  :value="item.id"
                  :id="'choice'+index"
                  v-model="checkedNum"
                  style="display:none"
                >
                <label :for="'choice'+index" class="choice-item flex-jc-between pd-15">
                  <span>{{item.name}}</span>
                  <span>{{item.price}}</span>
                </label>
              </li>
            </ul>
            <!-- <div>{{checkedNum}}</div> -->
            <div class="btn_next text-c" @click="toconfirOrder"></div>
          </div>

          <div v-show="ind ==1">
            <div v-for="(item,index) in beautician" :key="index" @click="gobeautyDetail(item.id)">
              <div class="flexbox flex-align-items">
                <div class="pd-lr-15">
                  <img class="img_header" :src="item.avatar">
                </div>

                <div class="flex-jc-between border-b right pd-tb-15 flex-align-items">
                  <div>
                    <div class="name">{{item.name}}</div>
                    <div class="ser_number">{{item.serve_num}}</div>
                    <div class="fz12 fc-fen"> {{item.grade}}%</div>
                  </div>
                  <div>
                    <div class="ser_btn" v-if="item.serve_status==1"></div>
                    <div class="xiuxi_btn" v-else></div>
                  </div>
                </div>
              </div>
            </div>
          </div>

          <div v-show="ind ==2">
            <div class="bgc">
              <div class="border-b">
                <div class="bgc pd-tb-15">
                  <div class="nav bgc">
                    <div
                      :class="{ 'selected': typenum==0 }"
                      @click="checkedGrade"
                    >({{comment.total_num}})</div>
                    <div
                      :class="{ 'selected': typenum==1 }"
                      @click="checkedGrade1"
                    >({{comment.good_num}})</div>
                    <div
                      :class="{ 'selected': typenum==2 }"
                      @click="checkedGrade2"
                    >({{comment.com_num}})</div>
                    <div
                      :class="{ 'selected': typenum==3 }"
                      @click="checkedGrade3"
                    >({{comment.bad_num}})</div>
                  </div>
                </div>
              </div>

              <div class="word_mouth pd-lr-15">
                <div v-for="(item,index ) in comment" :key="index">
                  <div class="flexbox pd-tb-15">
                    <div class="head_img">
                      <img :src="item.user.avatar">
                    </div>
                    <div class="pg-right">
                      <div class="flex-jc-between">
                        <div class="head_name">{{item.user.nickname}}</div>
                        <div>
                          <div class="flex-align-items" v-if="item.grade==1">
                            <img class="pingjia_img" src="../../assets/photo/haoping.png">
                            <span class="grey_12"></span>
                          </div>
                          <div class="flex-align-items" v-else-if="item.grade==2">
                            <img class="pingjia_img" src="../../assets/photo/yiban.png">
                            <span class="grey_12"></span>
                          </div>
                          <div class="flex-align-items" v-else>
                            <img class="pingjia_img" src="../../assets/photo/chaping.png">
                            <span class="grey_12"></span>
                          </div>
                        </div>
                      </div>

                      <div class="com_det">{{item.content}}</div>
                      <div class="imglist_b">
                        <div class="imglist" v-for="(items,indexs) in item.imglist" :key="indexs">
                          <img class="itemimg" :src="items">
                        </div>
                      </div>
                      <div class="flex-jc-between">
                        <div class="grey_12">:{{item.beautician.username}}</div>
                        <div class="grey_12">{{item.comment_time}}</div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </van-tab>
      </van-tabs>
    </div>

    <!-- </div> -->
  </div>
</template>

<script>
import wx from "weixin-js-sdk";
import { Toast } from "vant";
export default {
  data() {
    return {
      data: "",
      comment: "",
      navtitle: ["", "", ""],
      beautician: [],
      ind: 0,
      lat: "",
      lng: "",

      servelist: [
        // { name: "1", price: "69.00" },
        // { name: "1", price: "69.00" },
        // { name: "2", price: "69.00" },
        // { name: "3", price: "69.00" },
        // { name: "4", price: "69.00" }
      ],
      checkedNum: [],

      selectList: [],
      isborder: false,
      comment: [],
      wordlist: [
        {
          pingjia: "0",
          imglist: [
            "http://www.pptbz.com/pptpic/UploadFiles_6909/201306/2013062320262198.jpg",
            "http://www.pptbz.com/pptpic/UploadFiles_6909/201306/2013062320262198.jpg"
          ]
        },
        { pingjia: "1" },
        { pingjia: "2" }
      ],

      typenum: 0
      // bgimg: {
      //   background:
      //     "url(" +
      //     require("../../assets/photo/shopimg1.png") +
      //     ") no-repeat top",
      //   backgroundSize: "100% 100%"
      // }
    };
  },
  created() {
    this.getDetail();
    this.serveList();
  },
  methods: {
    selectIndex(index) {},

    getDetail() {
      Toast.loading({ mask: true, message: "..." });
      this.axios
        .get(this.API + "/api/v1/store/detail", {
          params: {
            store_id: this.$route.query.id
          }
        })
        .then(res => {
          console.log(res.data, "store/detail");
          let resdata = res.data;
          if (resdata.code == 200) {
            this.data = resdata.data;
            this.lat = resdata.data.lat;
            this.lng = resdata.data.lng;
            Toast.clear();
          } else {
            Toast.clear();
            Toast(resdata.msg);
          }
        });
    },
    location(data) {
      console.log(data);

      wx.openLocation({
        latitude: this.lat, // 90 ~ -90
        longitude: this.lng, // 180 ~ -180
        name: "", // 
        address: "", // 
        scale: 1, // ,,1~28
        infoUrl: "" // ,
      });
    },
    serveList() {
      Toast.loading({ mask: true, message: "..." });
      this.axios
        .get(this.API + "/api/v1/store/serve/list", {
          params: {
            store_id: this.$route.query.id
          }
        })
        .then(res => {
          console.log(res.data, "serve/list");
          let resdata = res.data;
          if (resdata.code == 200) {
            this.servelist = resdata.data;
            Toast.clear();
          } else {
            Toast.clear();
            Toast(resdata.msg);
          }
        });
    },
    ontab(index, title) {
      console.log(index, title);
      if (this.ind == 0) {
        this.serveList();
        return;
      }
      // 
      if (this.ind == 1) {
        Toast.loading({ mask: true, message: "..." });
        this.axios
          .get(this.API + "/api/v1/store/beautician/list", {
            params: {
              store_id: this.$route.query.id
            }
          })
          .then(res => {
            console.log(res.data, "beautician/list");
            let resdata = res.data;
            if (resdata.code == 200) {
              this.beautician = resdata.data;
              Toast.clear();
            } else {
              Toast.clear();
              Toast(resdata.msg);
            }
          });
        return;
      }
      // 
      if (this.ind == 2) {
        Toast.loading({ mask: true, message: "..." });
        this.axios
          .get(this.API + "/api/v1/store/comment/list", {
            params: {
              store_id: this.$route.query.id,
              grade: 0
            }
          })
          .then(res => {
            console.log(res.data, "comment/list");
            let resdata = res.data;
            if (resdata.code == 200) {
              this.comment = resdata.data.comment;

              Toast.clear();
            } else {
              Toast.clear();
              Toast(resdata.msg);
            }
          });
        return;
      }
    },

    checkedGrade() {
      this.typenum = 0;
      Toast.loading({ mask: true, message: "..." });
      this.axios
        .get(this.API + "/api/v1/store/comment/list", {
          params: {
            store_id: this.$route.query.id,
            grade: 0
          }
        })
        .then(res => {
          console.log(res.data, "comment/list");
          let resdata = res.data;
          if (resdata.code == 200) {
            this.comment = resdata.data.comment;
            Toast.clear();
          } else {
            Toast.clear();
            Toast(resdata.msg);
          }
        });
    },
    checkedGrade1() {
      this.typenum = 1;
      Toast.loading({ mask: true, message: "..." });
      this.axios
        .get(this.API + "/api/v1/store/comment/list", {
          params: {
            store_id: this.$route.query.id,
            grade: 1
          }
        })
        .then(res => {
          console.log(res.data, "comment/list");
          let resdata = res.data;
          if (resdata.code == 200) {
            this.comment = resdata.data.comment;
            Toast.clear();
          } else {
            Toast.clear();
            Toast(resdata.msg);
          }
        });
    },
    checkedGrade2() {
      this.typenum = 2;
      Toast.loading({ mask: true, message: "..." });
      this.axios
        .get(this.API + "/api/v1/store/comment/list", {
          params: {
            store_id: this.$route.query.id,
            grade: 2
          }
        })
        .then(res => {
          console.log(res.data, "comment/list");
          let resdata = res.data;
          if (resdata.code == 200) {
            this.comment = resdata.data.comment;
            Toast.clear();
          } else {
            Toast.clear();
            Toast(resdata.msg);
          }
        });
    },
    checkedGrade3() {
      this.typenum = 3;
      Toast.loading({ mask: true, message: "..." });
      this.axios
        .get(this.API + "/api/v1/store/comment/list", {
          params: {
            store_id: this.$route.query.id,
            grade: 3
          }
        })
        .then(res => {
          console.log(res.data, "comment/list");
          let resdata = res.data;
          if (resdata.code == 200) {
            this.comment = resdata.data.comment;
            Toast.clear();
          } else {
            Toast.clear();
            Toast(resdata.msg);
          }
        });
    },
    // 
    gobeautyDetail(id) {
      this.$router.push({
        path: "/beautyDetail",
        query: {
          id: id
        }
      });
    },
    // 
    toconfirOrder() {
      // console.log(JSON.stringify(this.checkedNum));
      console.log(this.checkedNum);
      // this.$router.push({
      //   path: "/confirOrder/",
      //   query: {
      //     checkedNum: this.checkedNum
      //   }
      // });
      let serve_ids = JSON.stringify(this.checkedNum);
      this.$router.push({
        path: "/confirOrder/",
        query: {
          serve_ids: serve_ids
        }
      });
      if (this.checkedNum.length != 0) {
        Toast.loading({ mask: true, message: "..." });

        let param = new URLSearchParams();
        param.append("serve_ids", serve_ids);
        this.axios.post(this.API + "/api/v1/serve/confirm", param).then(res => {
          console.log(res.data, "confirm");
          let resdata = res.data;
          if (resdata.code == 200) {
            this.comment = resdata.data;
            Toast.clear();
            this.$router.push({
              path: "/confirOrder/",
              query: {
                serve_ids: serve_ids
              }
            });
          } else {
            Toast.clear();
            Toast(resdata.msg);
          }
        });
      } else {
        Toast("");
      }
    }
  }
};
</script>
<style>
#shopnav .van-tabs__line {
  background-color: #fff;
  background-image: linear-gradient(90deg, #fe4171 100%, #ff5491 100%);
}
#shopnav .van-tab {
  color: black;
  font-size: 14px;
}
#shopnav .van-tab--active {
  color: #fe4171;
}
</style>
<style scoped>
.fz12 {
  font-size: 12px;
}

.img_shop {
  width: 100%;
  height: 160px;
}
.shopnav {
  background: #fff;
  margin: 0px 15px 0 15px;
  border-radius: 10px;
  box-shadow: 1px 2px 8px 0 rgba(207, 206, 206, 0.5);
  top: -30px;
  overflow: hidden;
}

.img_gouxuan {
  position: absolute;
  left: 269px;
  margin-top: 31px;
  width: 28px;
  height: 27px;
}
.btn_next {
  height: 35px;
  line-height: 35px;
  margin: 10px 0;
  background: linear-gradient(90deg, #ff5491, #fe4171);
  color: #fff;
  border-radius: 20px;
}
.shop {
  align-items: center;
  width: 100%;
  height: 180px;
}
.shop_mess {
  width: 290px;
  position: absolute;
  top: 0;
  color: #fff;
}
.shop_name {
  font-size: 16px;
}
.address {
  width: 200px;
}
.img_location {
  width: 12px;
  height: 15px;
}
.img_header {
  width: 60px;
  height: 60px;
  border-radius: 50%;
}

.right {
  width: 200px;
  padding-right: 15px;
  padding-left: 5px;
}
.name {
  font-size: 17px;
  font-weight: 600;
}
.ser_number {
  padding: 5px 0;
  font-size: 12px;
  color: #979797;
}
.right :last-child {
  border-bottom: 1px solid #fff;
}
.ser_btn {
  height: 25px;
  line-height: 25px;
  background: linear-gradient(90deg, #ff5491, #fe4171);
  border-radius: 20px;
  color: #fff;
  padding: 0 15px;
}
.xiuxi_btn {
  height: 25px;
  line-height: 25px;
  background: #a7a6a5;
  border-radius: 20px;
  padding: 0 15px;
}
.grey_12 {
  font-size: 12px;
  color: #979797;
}
.nav {
  margin: 0 11px;
}
.nav > div {
  height: 25px;
  width: 61px;
  line-height: 25px;
  display: inline-block;
  border-radius: 20px;
  text-align: center;
  border: 1px solid #8c8c8c;
  box-sizing: border-box;
  font-size: 10px;
  margin-right: 3px;
}
.nav .selected {
  color: #fff;
  background: linear-gradient(90deg, #fe4171, #ff5491);
  border: none;
}

.word_mouth {
  /* padding: 0 10px; */
  height: 100%;
}

.word_mouth > div {
  border-bottom: 1px solid #f0f0f0;
}

.word_mouth :last-child {
  border-bottom: 1px solid #fff;
}
.head_name {
  padding: 3px 0;
}
.pingjia_img {
  width: 16px;
  height: 16px;
  margin-right: 5px;
}
.head_img > img {
  width: 50px;
  height: 50px;
  border-radius: 50%;
}
.pg-right {
  width: 210px;
}
.imglist_b {
  padding: 5px 0;
}
.imglist {
  display: inline-block;
  margin-top: 4px;
}
.itemimg {
  width: 63px;
  height: 63px;
  margin-right: 7px;

  border-radius: 5px;
}

.dettail {
  font-size: 15px;
  font-weight: 600;
  padding: 13px 0;
}
.weight {
  padding: 10px 0;
}
.pro_img {
  width: 100%;
  height: 450px;
  border-radius: 5px;
  padding: 10px 0;
}
.footer {
  width: 100%;
  height: 50px;
  position: fixed;
  bottom: 0;
}

/**/
.choice-item {
  color: #000;
}
/*inputlabel*/
input:checked + .choice-item {
  background: url(../../assets/photo/gouxuan.png) no-repeat;
  background-size: 30px auto;
  background-position: 268px 31px;
  border: 1px solid #ff508b;
  color: #ff508b;
}
</style>


