<template>
  <div>
    <div class="pd-15 flex-align-items">
      <div class="flexbox bgc position tel_box" @click="show=true">
        <div></div>
        <div class="img_box">
          <div>
            <img class="img_sanjiao" src="../../assets/photo/icon-top.png">
          </div>
          <div>
            <img class="img_sanjiao1" src="../../assets/photo/icon-triangle.png">
          </div>
        </div>
      </div>
      <div>
        <input placeholder="" v-model="classval">
      </div>
    </div>
    <div class="pd-15 fz12 fc-grey">
      <span class="fc-fen">:</span>
      <span>
        
        
        
        
      </span>
    </div>
    <div class="pd-15">
      <div class="btn_box border-fen" @click="agree"></div>
    </div>
    <van-popup v-model="show" position="bottom" :close-on-click-overlay="false">
      <van-picker
        value-key="val"
        title=""
        :columns="sexList"
        show-toolbar
        @cancel="show = false"
        @confirm="onConfirm"
      />
    </van-popup>
  </div>
</template>

<script>
export default {
  data() {
    return {
      show: false,
      classval: "",
      sexList: [{ val: "1" }, { val: "2" }, { val: "3" }]
    };
  },
  methods: {
    agree() {
      // this.$router.push({ path: "/resultNo" });
      this.$router.push({ path: "/result" });
    },
    onConfirm(val) {
      console.log(val);
      this.classval = val.val;

      this.show = false;
    }
  }
};
</script>
<style scoped>
.fz12 {
  font-size: 12px;
}
.tel_box {
  border-radius: 5px;
  padding: 10px;
}
.img_box {
  padding: 10px;
}
.img_sanjiao {
  width: 10px;
  height: 11.5px;
  position: absolute;
  top: 9px;
}
.img_sanjiao1 {
  width: 9px;
  height: 7px;
  position: absolute;
  top: 22px;
}
input {
  padding: 11.5px;
  border-radius: 5px;
  margin-left: 15px;
}
input::placeholder {
  font-size: 12px;
}

.btn_box {
  margin-top: 60px;
  font-size: 13px;
  text-align: center;
  color: #fff;
  border-radius: 20px;
  padding: 10px;
  background-image: linear-gradient(90deg, #ff5491, #fe4171);
}
</style>





