<template>
  <div>
    <div class="pd-15 border-b bgc">
      <div class="title">{{data.title}}</div>

      <div class="flex-align-items pd-tb-15">
        <img class="img_logo border" src="../../assets/photo/yanzhengma.png">
        <span class="fz12" style="padding:10px"></span>
        <span class="fc-grey fz12">{{data.created_at}}</span>
      </div>
      <div class="pd-tb-15" v-html="data.description"></div>
      <div class="title"></div>
      <div class="flex-align-items pd-tb-15">
        <img class="img_header border" src="../../assets/photo/banner.png">
        <span class="fz12 welcome">~</span>
      </div>
      <div v-for="(item,index) in 2">
        <div class="flexbox mar-b-10">
          <img class="img_header border" src="../../assets/photo/kefu.png">
          <div class="right">
            <div class="fz12"></div>
            <div class="fc-grey fz12">2018-12-28 10:40:30</div>
            <div style="padding-top:5px" class="mar-b-10">9.9?</div>
            <div class="answer">():,,,,,</div>
          </div>
        </div>
      </div>
    </div>
    <div class="img_box flex-jc-around flex-align-items bgc">
      <div class="text-c border-l">
        <router-link to="/editAnswer">
          <img class="img_commit" src="../../assets/photo/commit.png">
        </router-link>
      </div>
      <div @click="ondianzan" class="text-c border-l">
        <img v-if="data.is_like==1" class="img_commit" src="../../assets/photo/dianzans.png">
        <img v-else class="img_commit" src="../../assets/photo/dianzan.png">
      </div>
      <div class="text-c border-l">
        <img class="img_fenxiang" src="../../assets/photo/fenxiang.png">
      </div>
    </div>
  </div>
</template>
<script>
import { Toast } from "vant";
export default {
  data() {
    return {
      isdianzan: false,
      data: ""
    };
  },
  created() {
    this.getData();
  },
  methods: {
    getData() {
      Toast.loading({ mask: true, message: "..." });
      this.axios
        .get(this.API + "/api/v1/question/detail", {
          params: {
            question_id: this.$route.query.id
          }
        })
        .then(res => {
          console.log(res.data, "report/detail");
          let resdata = res.data;
          if (resdata.code == 200) {
            this.data = resdata.data;
            Toast.clear();
          } else {
            Toast.clear();
            Toast(resdata.msg);
          }
        });
    },
    //
    ondianzan() {
      // this.isdianzan = !this.isdianzan;
      Toast.loading({ mask: true, message: "..." });

      let param = new URLSearchParams();
      param.append("question_id", this.$route.query.id);
      this.axios.post(this.API + "/api/v1/question/like", param).then(res => {
        console.log(res.data, "question/like");
        let resdata = res.data;
        if (resdata.code == 200) {
          console.log(resdata.msg);

          this.getData();
          Toast.success(resdata.msg);

          Toast.clear();
        } else {
        }

        Toast.clear();
      });
    }
  }
};
</script>
<style scoped>
.fz12 {
  font-size: 12px;
}
.title {
  font-size: 17px;
  font-weight: 600;
}
.img_logo {
  width: 22px;
  height: 22px;
  border-radius: 50%;
}
.right {
  padding-top: 10px;
}
.img_header {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  margin-right: 10px;
}
.welcome {
  width: 220px;
  background: #f0f0f0;
  border-radius: 20px;
  padding: 5px 10px;
}
.answer {
  width: 220px;
  background: #f0f0f0;
  padding: 5px 10px;
}
.img_box {
  height: 40px;
}
.img_box > div {
  width: 200px;
}
.border-l {
  border-left: #f0f0f0 1px solid;
}
.img_location {
  width: 11px;
  height: 14px;
}

.img_commit {
  width: 16px;
  height: 16px;
}
.img_fenxiang {
  width: 15px;
  height: 16px;
}
</style>



