<template>
  <div>
    <div class="bgc pd-15 border-b">
      <div class="title">{{data.title}}</div>
      <div class="flexbox pd-tb-15">
        <img class="img_logo border" src="../../assets/photo/yanzhengma.png">
        <div class="fc-grey fsz12">
          <span></span>
          <span>2018-12-28 10:40:30</span>
        </div>
      </div>

      <div class="imglist_box">
        <img class="itemimg" src="data.cover_url">
      </div>
      <div v-html="data.description"></div>
    </div>
    <div class="flex-align-items bgc img_box">
      <div class="text-c" @click="ondianzan">
        <div>
          <img v-if="data.is_like==1" class="img_commit" src="../../assets/photo/dianzans.png" alt>
          <img v-else class="img_commit" src="../../assets/photo/dianzan.png" alt>
        </div>
      </div>
      <div class="border-l text-c">
        <img class="img_fenxiang" src="../../assets/photo/fenxiang.png">
      </div>
    </div>
  </div>
</template>

<script>
import { Toast } from "vant";
export default {
  data() {
    return {
      data: "",
      isdianzan1: false
    };
  },
  created() {
    this.getData();
  },
  methods: {
    ondianzan() {
      Toast.loading({ mask: true, message: "..." });
      this.axios
        .get(this.API + "/api/v1/report/like", {
          params: {
            report_id: this.$route.query.id
          }
        })
        .then(res => {
          console.log(res.data, "report/like");
          let resdata = res.data;
          if (resdata.code == 200) {
            this.getData();
            Toast.clear();
          } else {
            Toast.clear();
            Toast(resdata.msg);
          }
        });
    },
    getData() {
      Toast.loading({ mask: true, message: "..." });
      this.axios
        .get(this.API + "/api/v1/report/detail", {
          params: {
            report_id: this.$route.query.id
          }
        })
        .then(res => {
          console.log(res.data, "report/detail");
          let resdata = res.data;
          if (resdata.code == 200) {
            this.data = resdata.data;
            Toast.clear();
          } else {
            Toast.clear();
            Toast(resdata.msg);
          }
        });
    }
  }
};
</script>
<style scoped>
.fsz12 {
  font-size: 12px;
}
.border-l {
  border-left: #f0f0f0 1px solid;
}
.title {
  font-size: 17px;
  font-weight: 600;
}
.img_logo {
  width: 20px;
  height: 20px;
  border-radius: 50%;
  margin-right: 10px;
}

.itemimg {
  width: 290px;
  height: 190px;
  margin-top: 10px;
}
.img_box {
  height: 40px;
}
.img_box > div {
  width: 200px;
}
.img_commit {
  width: 16px;
  height: 16px;
}
.img_fenxiang {
  width: 15px;
  height: 16px;
}
.photo {
  padding-top: 15px;
}
.photo img {
  border-radius: 10px;
}
</style>





