<template>
  <div>
    <div class="goods bgc mar-b-10 box-sizing flexbox">
      <img class="img_pro" :src="good.goods_img">
      <div class="flex-1 pd-10">
        <div style="font-weight: bold;" class="mar-b-10">{{good.goods_name}}</div>
        <div class="fc-grey">{{good.goods_num}}</div>
      </div>
    </div>

    <div class="border-b text-c bgc ratebox flex-align-items">
      <span class="pigfen"></span>
      <van-rate v-model="rateval" @change="onRating" :size="size"/>
      <span class="fc-fen"></span>
    </div>

    <div class="textarea bgc">
      <textarea rows="6" v-model.trim="contentval" placeholder="!"></textarea>
    </div>
    <div class="bgc imglist">
      <img
        @click="onImagePreview(index)"
        :src="item.content"
        v-for="(item,index) in imgarr"
        :key="index"
      >
      <van-uploader
        :after-read="onRead"
        accept="image/png, image/jpeg"
        multiple
        v-show="showupload"
      >
        <img src="../../assets/photo/uplode.png">
      </van-uploader>
    </div>

    <div class="pd-15">
      <div class="btn text-c" @click="submit"></div>
    </div>
  </div>
</template>

<script>
import { ImagePreview } from "vant";
import { Toast, Dialog } from "vant";
export default {
  data() {
    return {
      rateval: 5,
      size: 30,
      contentval: "",
      good: "",
      goods_id: "",
      order_id: "",
      imgarr: [],
      data: "",
      showupload: true
    };
  },
  watch: {
    imgarr() {
      if (this.imgarr.length == 3) {
        this.showupload = false;
      }
    }
  },
  created() {
    console.log(this.$route.query.good);
    let good = this.$route.query.good;
    this.goods_id = good.goods_id;
    this.order_id = good.order_id;
    this.good = good;
  },
  methods: {
    getdata() {},
    //
    onRating(e) {
      console.log(e);
      this.rateval = e;
    },
    //
    onRead(file) {
      console.log(file);
      // this.imgarr.push(file);
      this.imgarr = file;
    },
    //
    onImagePreview(index) {
      let arr = [];
      for (let v of this.imgarr) {
        arr.push(v.content);
      }
      ImagePreview({
        images: arr,
        startPosition: index
      });
    },
    submit() {
      if (this.contentval == "") {
        Toast("");
        return;
      }

      Toast.loading({ mask: true, message: "..." });
      let config = {
        headers: { "Content-Type": "multipart/form-data" }
      };

      let formData = new FormData();
      for (let v of this.imgarr) {
        formData.append("images", v.file);
      }
      formData.append("order_id", this.order_id);
      formData.append("goods_id", this.goods_id);
      formData.append("content", this.contentval);
      formData.append("star", this.rateval);

      this.axios
        .post(this.API + "/api/v1/order/comment", formData, config)
        .then(res => {
          console.log(res.data, "comment");
          let resdata = res.data;
          if (resdata.code == 200) {
            Toast.clear();
            // Dialog.alert({
            //   message: ""
            // }).then(e => {
            //   this.$router.go(-2);
            // });
          } else {
            Toast.clear();
            Toast(resdata.message);
          }
        });
    }
  }
};
</script>
<style>
.ratebox .van-rate__item {
  padding: 0 5px;
}
</style>

<style scoped>
.pd-10 {
  padding: 10px 0;
}
.goods {
  padding: 10px;
}
.goods img {
  width: 90px;
  height: 90px;
}
.img_pro {
  width: 60px;
  height: 70px;
  margin-right: 10px;
  border-radius: 5px;
}

.ratebox {
  padding: 10px 15px;
  justify-content: space-between;
}
.pigfen {
  font-size: 17px;
  font-weight: bold;
  margin-right: 10px;
}
.textarea {
  padding: 10px;
}
.textarea textarea {
  width: 100%;
}
.imglist {
  padding: 10px 0;
}
.imglist img {
  width: 77px;
  height: 77px;
  padding-left: 10px;
  padding-bottom: 10px;
}
.btn {
  margin-top: 76px;
  height: 42px;
  line-height: 42px;
  border-radius: 20px;
  color: #fff;
  background-image: linear-gradient(90deg, #fe4171, #fe4171 100%);
}
</style>



