<template>
  <div>
    <div class="pd-lr-15 sear_box flex-jc-between">
      <div class="flex-align-items" @click="show=true" style="width:80px">
        <span>{{areaval}}</span>
        <img class="img_sanjiao" src="../../assets/photo/sanjiao.png">
      </div>
      <!-- <div class="sear flex-align-items bgc" @click="gosearch">
        <van-icon name="search" size="20px" color="#979797"/>
        <span class="fc-grey fz12"></span>
      </div>-->
      <div id="searchs">
        <van-search
          v-model="value"
          placeholder=""
          show-action
          @search="onSearch"
          background="#f7f7f7"
        >
          <div slot="action" @click="onSearch" class="sear-btn"></div>
        </van-search>
      </div>
    </div>

    <div class="shoplist">
      <div v-for="(item,index) in list" :key="index" :title="item" @click="goshopDetail(item.id)">
        <div class="item">
          <div class="flex-jc-between">
            <div class="shop_title">{{item.name}}</div>
            <div>{{item.distance}}km</div>
          </div>
          <div class="flexbox" style=" padding-top: 10px;">
            <img class="img_location" src="../../assets/photo/location.png">
            <div class="fz12 fc-grey">{{item.address}}</div>
          </div>
        </div>
      </div>
    </div>

    <van-popup v-model="show" position="bottom" :close-on-click-overlay="false">
      <van-picker
        value-key="val"
        :columns="areaList"
        show-toolbar
        @cancel="show = false"
        @confirm="onConfirmArea"
      />
    </van-popup>
  </div>
</template>

<script>
import wx from "weixin-js-sdk";
import { Toast } from "vant";
export default {
  data() {
    return {
      list: [],
      value: "",
      areaval: "",
      areaList: [{ id: 0, val: "11" }, { id: 1, val: "22" }],
      show: false
    };
  },
  created() {
    this.getList();
    this.getLocation();
  },
  methods: {
    getLocation() {
      wx.getLocation({
        type: "wgs84", // wgs84gpsopenLocation'gcj02'
        success: function(res) {
          var latitude = res.latitude; // 90 ~ -90
          var longitude = res.longitude; // 180 ~ -180
          var speed = res.speed; // /
          var accuracy = res.accuracy; // 
        }
      });
    },
    getList() {
      Toast.loading({ mask: true, message: "..." });

      this.axios.get(this.API + "/api/v1/store/list").then(res => {
        console.log(res.data, "list");
        let resdata = res.data;
        if (resdata.code == 200) {
          this.list = resdata.data;
          Toast.clear();
        } else {
          Toast.clear();
          Toast(resdata.msg);
        }
      });
    },
    // 
    // gosearch() {},
    onSearch() {
      console.log(this.value);
    },
    goshopDetail(id) {
      this.$router.push({
        path: "/shopDetail",
        query: {
          id: id
        }
      });
    },
    onConfirmArea(val) {
      console.log(val);
      this.areaval = val.val;
      this.show = false;
    }
  }
};
</script>


<style>
#searchs {
  width: 250px;
}
#searchs .van-search__content {
  border-radius: 20px;
  background: #fff;
}

#searchs .van-search {
  padding: 0;
  background: red;
}
</style>
<style scoped>
.fz12 {
  font-size: 12px;
}
.img_sanjiao {
  width: 12px;
  height: 10px;
  margin-right: 10px;
}
.sear_box {
  padding-top: 10px;
  font-size: 13px;
}
/* .sear {
  width: 220px;
  height: 30px;
  line-height: 30px;
  border-radius: 20px;
  background: #fff;
}

.sear .van-icon {
  padding-left: 80px;
} */
.sear-btn {
}
.shoplist {
  margin-bottom: 60px;
}
.item {
  background: #fff;
  padding: 10px 20px;
  margin: 12px 14px 0 14px;
  border-radius: 10px;
  box-shadow: 1px 0px 6px 1px rgba(237, 237, 237, 1);
}
.shop_title {
  font-size: 15px;
  font-weight: 600;
}
.img_sanjiao {
  width: 12px;
  height: 10px;
}
.img_location {
  width: 12px;
  height: 16px;
  margin-right: 5px;
}
</style>


