<template>
  <div>
    <div class="swiper-container">
      <div class="swiper-wrapper flex-align-items">
        <div class="swiper-slide" v-for="item in data">
          <img :src="item.img_url">
          <div class="card_name">{{item.name}}</div>
          <div class="card_num">
            <span>{{item.use_num}}</span>
          </div>
          <div class="card_price flex-align-items">
            <span></span>
            <span class="card_pri">{{item.mall_price}}</span>
          </div>
        </div>
      </div>
    </div>

    <div class="bgc flex-jc-between pd-15 mar-b-10">
      <div>
        {{name}}
        <span class="fz12">(:1)</span>
      </div>
      <div class="fc-fen fz15">{{mall_price}}</div>
    </div>

    <div class="bgc pd-lr-15">
      <div class="border-b title pd-tb-15">{{mall_price}}VIP{{name}}</div>
      <div>
        <div
          class="pd-tb-15 border-b"
          v-show="activeIndex === 0||activeIndex === 1||activeIndex === 3"
        >A111588</div>
        <div class="pd-tb10">
          <div class="bgc flex-jc-between fz12 pd-b10" v-for="(item,index) in 5">
            <div>+</div>
            <div>2</div>
          </div>
        </div>
      </div>
    </div>
    <div class="bgc footer border-t flex-jc-between">
      <div class="fc-fen price">: {{mall_price}}</div>
      <div class="btn bcol" @click="goPay(card_id)"></div>
    </div>
  </div>
</template>

<script>
import wx from "weixin-js-sdk";
import Swiper from "swiper";
import { Toast } from "vant";
export default {
  data() {
    return {
      price: "388.00",
      activeIndex: 0,
      mall_price: "",
      name: "",
      data: [],
      card_id: ""
    };
  },
  watch: {
    activeIndex: function(val) {
      console.log("6666" + val);
      let datalist = this.data;
      console.log(datalist);

      this.mall_price = datalist[val].mall_price;
      this.name = datalist[val].name;
      this.card_id = datalist[val].id;
    }
  },
  created() {
    this.getCard();
    console.log(wx, "wx");
  },
  methods: {
    initSwiper() {
      let self = this;
      var mySwiper = new Swiper(".swiper-container", {
        direction: "horizontal",
        loop: true, // 
        loopAdditionalSlides: 2,
        spaceBetween: 5,
        watchSlidesProgress: true,
        observer: true,
        effect: "coverflow",

        // slidesPerView: "auto",
        // freeMode: true,
        // slidesOffsetAfter: 15,
        // roundLengths: true,

        on: {
          touchEnd(e) {
            if (self.checkRepeat) {
              clearTimeout(self.checkRepeat);
            }

            self.checkRepeat = setTimeout(() => {
              self.activeIndex = mySwiper.realIndex;
            }, 500);
          }
        }
      });
    },
    getCard() {
      Toast.loading({ mask: true, message: "..." });
      var params = {
        order_status: this.$route.query.id
      };
      this.axios.get(this.API + "/api/v1/card/list", params).then(res => {
        console.log(res.data, "card");
        let resdata = res.data;
        if (resdata.code == 200) {
          this.data = resdata.data;
          this.mall_price = this.data[0].mall_price;
          this.name = this.data[0].name;
          Toast.clear();
        } else {
          Toast.clear();
          Toast(resdata.msg);
        }
      });
    },

    goPay() {
      let datalist = this.data;
      console.log(datalist, "datalist");

      if (this.card_id) {
        Toast.loading({ mask: true, message: "..." });
        let param = new URLSearchParams();
        param.append("card_id", this.card_id);
        this.axios.post(this.API + "/api/v1/card/submit", param).then(res => {
          let resdata = res.data;
          console.log(res.data, "card/submit");
          if (resdata.code == 200) {
            let order_id = resdata.data.id;

            console.log(order_id, "order_idorder_id");

            let param = new URLSearchParams();
            param.append("order_id", order_id);

            this.axios.post(this.API + "/api/v1/card/pay", param).then(res => {
              let resdata = res.data;
              if (resdata.code == 200) {
                wx.config({
                  debug: true,
                  appId: resdata.data.appId, // 
                  nonceStr: resdata.data.nonceStr, // 
                  signature: resdata.data.signature, // 1
                  timestamp: resdata.data.timestamp, // 
                  jsApiList: ["chooseWXPay"]
                });

                wx.chooseWXPay({
                  timestamp: resdata.data.timestamp, // jssdktimestamptimeStampS
                  nonceStr: resdata.data.nonceStr, //  32 
                  package: resdata.data.package, // prepay_idprepay_id=\*\*\*
                  signType: resdata.data.signType, // 'SHA1''MD5'
                  paySign: resdata.data.paySign, // 
                  success: function(res) {
                    // 
                    Toast("");
                    this.$router.go(-1);
                  }
                });
              } else {
                Toast.clear();
                Toast(resdata.msg);
              }
            });
          } else {
            Toast.clear();
            Toast(resdata.msg);
          }
        });
      } else {
        for (let i = 0; i < datalist.length; i++) {
          var card_id0 = datalist[0].id;
        }
        console.log(card_id0);
        Toast.loading({ mask: true, message: "..." });
        let param = new URLSearchParams();
        param.append("card_id", card_id0);
        this.axios.post(this.API + "/api/v1/card/submit", param).then(res => {
          let resdata = res.data;
          console.log(res.data, "card/submit");
          if (resdata.code == 200) {
            let order_id = resdata.data.id;
            let param = new URLSearchParams();
            param.append("order_id", order_id);
            this.axios.post(this.API + "/api/v1/card/pay", param).then(res => {
              let resdata = res.data;
              console.log(res.data, "card/pay");
              if (resdata.code == 200) {
                // wx.chooseWXPay({
                //   timestamp: resdata.data.timeStamp, // jssdktimestamptimeStampS
                //   nonceStr: resdata.data.nonceStr, //  32 
                //   package: resdata.data.package, // prepay_idprepay_id=\*\*\*
                //   signType: resdata.data.signType, // 'SHA1''MD5'
                //   paySign: resdata.data.paySign, // 
                //   success: function(res) {
                //     // 
                //   }
                // });
              } else {
                Toast.clear();
                Toast(resdata.msg);
              }
            });
          } else {
            Toast.clear();
            Toast(resdata.msg);
          }
        });
      }
    }
  },
  mounted() {
    this.initSwiper();
  },
  beforeDestroy() {
    clearTimeout(this.checkRepeat);
  }
};
</script>
<style scoped>
.swiper-container {
  width: 190px;
  height: 120px;
  padding: 10px 0;
  overflow: visible !important;
  position: relative;
}
.swiper-container .swiper-wrapper .swiper-slide {
  width: 100%;
}

.swiper-container .swiper-wrapper .swiper-slide img {
  width: 100%;
  height: 120px;
}

.swiper-container .swiper-wrapper .swiper-slide-prev {
  height: 120px !important;
}
.swiper-container .swiper-wrapper .swiper-slide-prev img {
  height: 120px !important;
}
.swiper-container .swiper-wrapper .swiper-slide-next {
  height: 120px !important;
}

.swiper-container .swiper-wrapper .swiper-slide-next img {
  height: 120px !important;
}

.swiper-container .swiper-wrapper .swiper-slide-active {
  width: 190px;
}

.swiper-pagination {
  bottom: 5px !important;
}
.card_name {
  position: absolute;
  top: 15px;
  left: 10px;
  color: #fff;
  font-size: 17px;
}
.card_num {
  position: absolute;
  top: 93px;
  left: 10px;
  color: #fff;
  font-size: 12px;
}

.card_price {
  position: absolute;
  top: 90px;
  right: 10px;
  color: #fff;
}
.card_pri {
  font-size: 20px;
  font-weight: 700;
}
.fz12 {
  font-size: 12px;
}
.fz15 {
  font-size: 15px;
}
.pd-tb10 {
  padding: 10px 0;
}
.pd-b10 {
  padding-bottom: 10px;
}
.pic {
  margin-left: 10px;
}

.title {
  font-size: 15px;
}

.footer {
  width: 100%;
  height: 40px;
  position: fixed;
  right: 0;
  bottom: 0;
}
.btn {
  height: 40px;
  line-height: 40px;
  width: 108px;
  font-size: 14px;
  text-align: center;
}
.price {
  height: 40px;
  line-height: 40px;
  padding-left: 100px;
}
.bcol {
  background: linear-gradient(90deg, #ff5491, #fe4171);
  color: #fff;
}
</style>




