<template>
  <div>
    <div class="textarea bgc">
      <textarea rows="6" v-model.trim="contentval" placeholder="......"></textarea>
    </div>

    <div class="pd-15">
      <div class="btn text-c" @click="submit"></div>
    </div>
  </div>
</template>

<script>
import { Toast, Dialog } from "vant";
export default {
  data() {
    return {
      contentval: ""
    };
  },
  watch: {},
  created() {},
  methods: {
    submit() {
      this.$router.back(-1);
    }
  }
};
</script>


<style scoped>
.textarea {
  padding: 10px;
}
.textarea textarea {
  width: 100%;
}

.btn {
  margin-top: 20px;
  height: 40px;
  line-height: 40px;
  border-radius: 20px;
  color: #fff;
  background: linear-gradient(90deg, #ff5491, #fe4171);
}
</style>



