<template>
  <div>
    <div class="pd-15">
      <div class="fuwu_box bgc">
        <div class="pd-15">
          <span>:</span>
          <span></span>
        </div>
        <div class="pd-15">
          <span>:</span>
          <span class="fc-grey text-line">18822568982</span>
        </div>
        <div class="pd-15">
          <span>:</span>
          <span class="fc-grey">2018-08-02</span>
        </div>
        <div class="pd-15">
          <span>:</span>
          <span class="fc-grey"></span>
        </div>
        <div class="pd-15 flex-jc-between" @click="show=true">
          <div>
            <span class="fc-fen">:</span>
            <span>{{classval}}</span>
          </div>
          <div>
            <img class="img_sanjiao" src="../../assets/photo/icon-triangle.png">
          </div>
        </div>
      </div>

      <div class="btn_box border-fen" @click="agree"></div>
    </div>

    <van-popup v-model="show" position="bottom" :close-on-click-overlay="false">
      <van-picker
        value-key="val"
        title=""
        :columns="sexList"
        show-toolbar
        @cancel="show = false"
        @confirm="onConfirm"
      />
    </van-popup>
  </div>
</template>

<script>
import { Toast } from "vant";
export default {
  data() {
    return {
      show: false,
      classval: "",
      data: "",
      sexList: [
        { id: 0, val: "" },
        { id: 1, val: "" },
        { id: 1, val: "" }
      ]
    };
  },
  created() {
    this.getdata();
  },
  methods: {
    getdata() {
      Toast.loading({ mask: true, message: "..." });
      this.axios.get(this.API + "/api/v1/level").then(res => {
        console.log(res.data, "level");

        let resdata = res.data;
        if (resdata.code == 200) {
          this.data = resdata.data;

          Toast.clear();
        } else {
          Toast.clear();
          Toast(resdata.msg);
        }
      });
    },
    agree() {
      this.$router.back(-1);
    },
    onConfirm(val) {
      console.log(val);
      this.classval = val.val;

      this.show = false;
    },
    // 

    toserDetail() {
      this.$router.push({ path: "/serviceDetail/" });
    }
  }
};
</script>



<style scoped>
.fz15 {
  font-size: 15px;
}
.fzc13 {
  font-size: 13px;
}

.fuwu_box {
  box-shadow: 1px 2px 8px 0px rgba(207, 206, 206, 0.5);
  border-radius: 10px;
  overflow: hidden;
  padding-bottom: 15px;
}

.btn_box {
  margin-top: 60px;
  font-size: 13px;
  text-align: center;
  color: #fff;
  border-radius: 20px;
  padding: 10px;
  background-image: linear-gradient(90deg, #ff5491, #fe4171);
}

.img_sanjiao {
  width: 10px;
  height: 6px;
}
</style>


