<template>
  <div>
    <div class="bgc">
      <div class="bgc pd-15">
        <div class="sear" @click="gosearch">
          <van-icon name="search" size="14px" color="#979797"/>
          <span class="fc-grey fz12"></span>
        </div>
      </div>
    </div>
    <div id="mernav">
      <van-tabs @click="ontab">
        <van-tab :title="item" v-for="(item,index) in narrlist" :key="index">
          <div class="nav bgc fc-grey border-b">
            <div :class="{ 'selected': typenum==0 }" @click="show=true">
              {{areaval}}
              <img class="img_sanjiao" src="../../assets/photo/icon-triangle.png">
            </div>
            <div :class="{ 'selected': typenum==1 }" @click="typenum=1"></div>
            <div :class="{ 'selected': typenum==2 }" @click="typenum=2"></div>
            <div :class="{ 'selected': typenum==3 }" @click="typenum=3"></div>
          </div>

          <van-popup v-model="show" position="bottom" :close-on-click-overlay="false">
            <van-picker
              value-key="val"
              :columns="areaList"
              show-toolbar
              @cancel="show = false"
              @confirm="onConfirmArea"
            />
          </van-popup>
          <div v-show="typenum==0||typenum==1||typenum==2||typenum==3">
            <div class="bgc merch-box">
              <div class="flexbox merchlist" v-for="(item,index) in 3" @click="merchantDetail">
                <img class="img_slt" src="../../assets/photo/banner.png">
                <div class="flex-column-space-between right">
                  <div class="flex-jc-between">
                    <div></div>
                    <div>1.2km</div>
                  </div>
                  <div class="fsz12 flex-align-items">
                    <van-rate
                      v-model="value"
                      :size="10"
                      :count="5"
                      color="#FFB10E"
                      void-color="#ceefe8"
                    />
                    <span>9</span>
                  </div>
                  <div class="pro_title fsz12" @click.stop="gonavigation">
                    <img class="img_location" src="../../assets/photo/location.png">
                    <div style="width: 190px; " class="newline">
                      788(
                      788(
                      
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </van-tab>
      </van-tabs>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      active: 0,
      narrlist: ["", "", "", "", ""],
      typenum: 0,
      show: false,
      value: 5,
      areaval: "",
      areaList: [
        { id: 0, val: "" },
        { id: 1, val: "" },
        { id: 1, val: "11" }
      ]
    };
  },
  methods: {
    ontab(index, title) {
      console.log(index, title);
    },
    gosearch() {
      this.$router.push({ path: "/search/" });
    },
    // 
    merchantDetail() {
      this.$router.push({ path: "/merchantDetail/" });
    },
    gonavigation() {
      this.$router.push({ path: "/navigation/" });
    },
    onConfirmArea(val) {
      console.log(val);
      this.areaval = val.val;
      this.show = false;
    }
  }
};
</script>
<style>
#mernav .van-tabs__line {
  background-color: #fff;
  background-image: linear-gradient(90deg, #fe4171 100%, #ff5491 100%);
}
#mernav .van-tab {
  color: black;
}
#mernav .van-ellipsis {
  font-size: 14px;
}
#mernav .van-tab--active {
  color: #fe4171;
}
</style>

<style scoped>
.fz12 {
  font-size: 12px;
}
.sear {
  height: 30px;
  line-height: 30px;
  border-radius: 20px;
  background: #f6f5f5;
}
.img_sanjiao {
  width: 8px;
  height: 6px;
}
.sear .van-icon {
  padding-left: 100px;
}

.nav {
  padding: 5px 10px;
}
.nav > div {
  width: 68px;
  height: 25px;
  line-height: 25px;
  display: inline-block;
  text-align: center;
  box-sizing: border-box;
  font-size: 12px;
  margin-right: 3px;
}
.nav .selected {
  color: #fe4171;
}

.fsz12 {
  font-size: 12px;
}
.fsz10 {
  font-size: 10px;
}

.merchlist {
  border-bottom: 10px solid #f0f0f0;
}
.merchlist:last-child {
  border: none;
}
.img_slt {
  width: 80px;
  height: 80px;
  padding: 15px;
  border-radius: 20px;
}
.right {
  width: 200px;
  padding: 15px 0;
}
.pro_title {
  display: flex;
  color: #979797;
  font-size: 12px;
}
.img_location {
  width: 11px;
  height: 14px;

  padding: 3px 5px 0 0;
}
</style>



