<template>
  <div>
    <div class="flexbox flex-align-items bgc pd-tb-15">
      <div class="pd-lr-15">
        <img class="img_header" :src="data.avatar">
      </div>

      <div>
        <div class="name">{{data.name}}</div>
        <div class="fz12 fc-grey ser_number">{{data.serve_num}}</div>
        <div class="fc-fen"> {{data.grade}}%</div>
      </div>
    </div>
    <div class="bgc pd-15 address">
      <div>{{store.name}}</div>
      <div class="flexbox" style=" padding-top: 10px;">
        <img class="img_location" src="../../assets/photo/location.png">
        <div class="fz12 fc-grey">{{store.province+store.city+store.district+store.address}}</div>
      </div>
    </div>
    <div class="word_mouth_box bgc">
      <div class="flex-jc-between pd-15 border-b" @click="toMore(data.id)">
        <div></div>
        <div class="fc-grey flex-align-items">
          
          <van-icon name="arrow"/>
        </div>
      </div>
      <div class="word_mouth pd-lr-15">
        <div v-for="(item,index ) in comment" :key="index">
          <div class="flexbox pd-tb-15">
            <div class="head_img">
              <img src="../../assets/photo/dpfc.png">
            </div>
            <div class="pg-right">
              <div class="flex-jc-between">
                <div class="head_name">{{item.user.nickname}}</div>
                <div class="grey_12">
                  <div class="flex-align-items" v-if="item.grade==1">
                    <img class="pingjia_img" src="../../assets/photo/haoping.png">
                    <span></span>
                  </div>
                  <div class="flex-align-items" v-else-if="item.grade==2">
                    <img class="pingjia_img" src="../../assets/photo/yiban.png">
                    <span></span>
                  </div>
                  <div class="flex-align-items" v-else>
                    <img class="pingjia_img" src="../../assets/photo/chaping.png">
                    <span></span>
                  </div>
                </div>
              </div>

              <div class="com_det">{{item.content}}</div>
              <div class="imglist_b">
                <div class="imglist" v-for="(items,indexs) in item.imglist" :key="indexs">
                  <img class="itemimg" :src="items">
                </div>
              </div>
              <div class="flex-jc-between grey_12">
                <div>:{{item.beautician.username}}</div>
                <div>{{item.comment_time}}</div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { Toast } from "vant";
export default {
  data() {
    return {
      state: 1,
      data: "",
      comment: [],
      store: "",
      wordlist: [
        {
          pingjia: "0",
          imglist: [
            "http://www.pptbz.com/pptpic/UploadFiles_6909/201306/2013062320262198.jpg",
            "http://www.pptbz.com/pptpic/UploadFiles_6909/201306/2013062320262198.jpg",
            "http://www.pptbz.com/pptpic/UploadFiles_6909/201306/2013062320262198.jpg"
          ]
        },
        { pingjia: "1" },
        { pingjia: "2" }
      ]
    };
  },
  created() {
    this.getDetail();
  },
  methods: {
    getDetail() {
      Toast.loading({ mask: true, message: "..." });
      this.axios
        .get(this.API + "/api/v1/store/beautician/detail", {
          params: {
            beautician_id: this.$route.query.id
          }
        })
        .then(res => {
          console.log(res.data, "beautician/detail");
          let resdata = res.data;
          if (resdata.code == 200) {
            this.data = resdata.data;
            this.comment = resdata.data.comment;
            this.store = resdata.data.store;
            console.log(resdata.data.comment);
            Toast.clear();
          } else {
            Toast.clear();
            Toast(resdata.msg);
          }
        });
    },
    // 
    toMore(id) {
      this.$router.push({
        path: "/beautyComment",
        query: {
          id: id
        }
      });
    }
  }
};
</script>

<style scoped>
.fz12 {
  font-size: 12px;
}

.name {
  font-size: 18px;
  font-weight: 600;
}
.ser_number {
  padding: 5px 0;
}
.img_header {
  width: 60px;
  height: 60px;
  border-radius: 50%;
}

.address {
  margin-top: 10px;
}
.img_location {
  width: 12px;
  height: 16px;
  margin-right: 10px;
}
.grey_12 {
  font-size: 12px;
  color: #979797;
}
.word_mouth_box {
  margin-top: 10px;
}

.word_mouth {
  height: 100%;
}
.pg-right {
  width: 250px;
}
.word_mouth > div {
  border-bottom: 1px solid #f0f0f0;
}

.word_mouth :last-child {
  border-bottom: 1px solid #fff;
}
.head_name {
  padding: 3px 0;
}
.pingjia_img {
  width: 16px;
  height: 16px;
  margin-right: 5px;
}
.head_img > img {
  width: 50px;
  height: 50px;
  border-radius: 50%;
}
.imglist_b {
  padding: 5px 0;
}
.imglist {
  display: inline-block;
  margin-top: 4px;
}
.itemimg {
  width: 63px;
  height: 63px;
  margin-right: 7px;
  border-radius: 5px;
}
</style>




