<template>
  <div>
    <div class="myinfor">
      <div class="me bgc">
        <van-cell center>
          <template slot="title">
            <span></span>
            <input type="text" v-model.trim="users_name">
          </template>
        </van-cell>

        <van-cell is-link center :value="sexval" @click="showsex=true">
          <template slot="title">
            <div></div>
          </template>
        </van-cell>

        <van-cell
          is-link
          center
          :value="areaval==''?'':areaval[0].name+areaval[1].name+areaval[2].name"
          @click="showarea=true"
        >
          <template slot="title">
            <div></div>
          </template>
        </van-cell>
        <div class="border-b dz_detail">
          <span></span>
          <input type="text" placeholder="" v-model.trim="detailval">
        </div>

        <van-cell is-link center :value="datetext==''?'':datetext" @click="showdate=true">
          <template slot="title">
            <div></div>
          </template>
        </van-cell>
      </div>
    </div>

    <div class="pd-15">
      <div class="btn text-c" @click="submit"></div>
    </div>

    <van-popup v-model="showarea" position="bottom" :close-on-click-overlay="false">
      <van-area :area-list="areaList" @cancel="showarea=false" @confirm="onconfirmArea"/>
    </van-popup>

    <van-popup v-model="showsex" position="bottom" :close-on-click-overlay="false">
      <van-picker
        value-key="val"
        :columns="sexList"
        show-toolbar
        @cancel="showsex = false"
        @confirm="onConfirmSex"
      />
    </van-popup>

    <van-popup v-model="showdate" position="bottom" :close-on-click-overlay="false">
      <van-datetime-picker
        v-model="dateval"
        type="date"
        :min-date="startDate"
        :max-date="endDate"
        @cancel="showdate = false"
        @confirm="onConfirmDate"
      />
    </van-popup>
  </div>
</template>

<script>
import { Toast } from "vant";
import area from "@/utils/area.js";
export default {
  data() {
    return {
      users_name: "",

      showarea: false,
      areaval: "",
      areaList: area,

      showsex: false,
      sexval: "",
      sex_id: "",
      sexList: [{ id: 0, val: "" }, { id: 1, val: "" }],
      detailval: "",
      dateval: new Date(),
      datetext: "",
      showdate: false,
      startDate: new Date("1899/01/01 00:00"),
      endDate: new Date()
    };
  },
  created() {},

  methods: {
    onconfirmArea(val) {
      console.log(val);
      this.areaval = val;
      this.showarea = false;
    },
    onConfirmSex(val) {
      console.log(val);
      this.sexval = val.val;

      this.showsex = false;
    },
    onConfirmDate(val) {
      console.log(val.getFullYear(), val.getMonth() + 1, val.getDate());
      this.dateval = val;
      let datet = [val.getFullYear(), val.getMonth() + 1, val.getDate()];
      console.log(datet);
      this.datetext = `${datet[0]}${datet[1]}${datet[2]}`;
      this.showdate = false;
    },

    submit() {
      let sex_id = "";
      if (this.sexval == "") {
        sex_id = 0;
      } else {
        sex_id = 1;
      }
    }
  }
};
</script>

<style scoped>
.btn {
  height: 42px;
  line-height: 42px;
  border-radius: 20px;
  color: #fff;
  background-image: linear-gradient(90deg, #2dbbf1 0%, #4ea9f9 100%);
}
.myinfor {
  margin: 15px;
  border-radius: 10px;
  overflow: hidden;
}

.me {
  width: 300px;
  height: 100%;
}
.tx {
  padding: 10px 0;
  font-size: 12px;
}
.dz_detail {
  padding: 10px;
  font-size: 13px;
}
.dz_detail input {
  width: 75%;
  text-align: right;
  padding-right: 10px;
  color: grey;
}
.dz_detail input::placeholder {
  text-align: right;
}
.head_img {
  width: 45px;
  height: 45px;
  border-radius: 50%;
  margin-right: 10px;
}
.head_img > img {
  width: 45px;
  height: 45px;
}
</style>


