<template>
  <div class="bgc pd-15">
    <div class="order_box mar-b-10" v-for="(item,index) in applyorder">
      <div class="flex-jc-between border-b top pd-tb-15">
        <div class="fz12">{{item.serve_time}}</div>
        <div class="fc-fen" v-if="item.order_status==1"></div>
        <div class="fc-fen" v-else-if="item.order_status==4"></div>
        <div class="fcol" v-else></div>
      </div>
      <div class="flexbox border-b content pd-tb-15">
        <img class="img_head" :src="item.beautician.avatar">

        <div class="right">
          <div>{{item.beautician.username}}</div>
          <div class="fc-grey fz12">{{item.store.name}}</div>
          <div class="flexbox">
            :
            <div v-for="(items,indexs) in item.serve">
              <span style="padding-right:4px">{{items.serve_name}}</span>
            </div>
          </div>
          <div>:{{item.total_price}}</div>
        </div>
      </div>
      <!-- <div class="text-r pd-lr-15 btn">
        <span class="border">{{item.btn_state}}</span>
        <span class="border"></span>
      </div>-->
      <div class="text-r pd-lr-15 btn" @click="onshowmodel" v-if="item.order_status==1">
        <span class="border"></span>
      </div>

      <div class="text-r pd-lr-15 btn" @click="toComment(item.id)" v-else-if="item.order_status==4">
        <span class="border-fen fc-fen"></span>
      </div>

      <div class="text-r pd-lr-15 btn" @click="del(index)" v-else>
        <span class="border"></span>
      </div>
    </div>

    <van-popup v-model="showmodel" position="bottom" :close-on-click-overlay="false">
      <div class="text-c position title">
        
        <div class="closeicon">
          <van-icon name="close" @click="onshowmodel"/>
        </div>
      </div>
      <div class="pd-lr-15">
        <div class="pd-tb-15">,</div>
        <van-radio-group v-model="radio">
          <div
            class="flex-jc-between mar-b-10"
            @click="radio = index"
            v-for="(item,index) in canceltext"
            :key="index"
          >
            <span>{{index+1}}{{item.text}}</span>
            <van-radio :name="index" checked-color="#FE4171"></van-radio>
          </div>
        </van-radio-group>
      </div>
      <div class="pd-15">
        <div class="btn1 text-c" @click="cancelOrder"></div>
      </div>
    </van-popup>
  </div>
</template>
<script>
import { Dialog, Toast } from "vant";
export default {
  data() {
    return {
      radio: 0,
      applyorder: [],
      canceltext: [
        { id: 1, text: "/" },
        { id: 2, text: "" },
        { id: 3, text: "" },
        { id: 4, text: "" }
      ],
      showmodel: false,
      orderlist: [
        { state: "0", btn_state: "0" },
        { state: "1", btn_state: "1" },
        { state: "2", btn_state: "2" }
      ]
    };
  },
  created() {
    this.getData();
  },
  methods: {
    getData() {
      Toast.loading({ mask: true, message: "..." });
      this.axios.get(this.API + "/api/v1/serve/order").then(res => {
        console.log(res.data, "serve/order");
        let resdata = res.data;
        if (resdata.code == 200) {
          this.applyorder = resdata.data;
          Toast.clear();
        } else {
          Toast.clear();
          Toast(resdata.msg);
        }
      });
    },

    toComment(id) {
      this.$router.push({
        path: "/commentt/",
        query: {
          id: id
        }
      });
    },
    onshowmodel() {
      if (this.showmodel) {
        this.showmodel = false;
      } else {
        this.showmodel = true;
      }
    },
    cancelOrder() {
      this.showmodel = false;
    },
    del(index) {
      console.log(index);
      this.orderlist.splice(index, 1);
      Toast("");
    }
  }
};
</script>

<style scoped>
.fz12 {
  font-size: 12px;
}
.fcol {
  color: #686364;
}
.top {
  margin-left: 15px;
  padding-right: 15px;
}
.order_box {
  background-color: rgba(255, 255, 255, 1);
  box-shadow: 1px 2px 8px 0px rgba(207, 206, 206, 0.5);
  border-radius: 5px;
}
.content {
  margin-left: 15px;
  padding-right: 15px;
}
.right {
  margin-left: 12px;
}
.right div {
  line-height: 20px;
}
.img_head {
  width: 60px;
  height: 60px;
  border-radius: 50%;
}
.btn {
  height: 45px;
  line-height: 45px;
}
.btn span {
  padding: 5px 10px;
  border-radius: 5px;
}

.title .closeicon {
  position: absolute;
  right: 10px;
  top: 10px;
  font-size: 20px;
}
.title {
  font-size: 16px;
  padding-top: 15px;
}
.btn1 {
  height: 30px;
  line-height: 30px;
  border-radius: 20px;
  color: #fff;
  background-image: linear-gradient(90deg, #fe4171, #fe4171 100%);
}
</style>



